import { Component, OnInit, AfterViewInit, OnDestroy } from '@angular/core';
import { UserSession } from '@core/models/user-session';
import { DoctorService } from '../../services/doctor.service';
import { UserService } from '@core/services';
import { MatTabChangeEvent } from '@angular/material';
import * as moment from 'moment';
import { Subscription } from 'rxjs';
import { AutoUnsubscribe } from 'ngx-auto-unsubscribe';
import { AppointmentApiService } from '@core/services/appointment-api.service';
import { ToastrService } from 'ngx-toastr';

@AutoUnsubscribe()
@Component({
  selector: 'app-day-appointment-calendar',
  templateUrl: './day-appointment-calendar.component.html',
  styleUrls: ['./day-appointment-calendar.component.scss']
})
export class DayAppointmentCalendarComponent
  implements OnInit, AfterViewInit, OnDestroy {

  public cancelledSideNav = false;
  public cancelSideNav = true;
  public clickedDate: any;
  public clickedDay: string;
  public docAppointments: any;
  public doctorAppSlots: any;
  public doctorEmpId: any;
  public doctorSlotTimings: any;
  public isSlotsAvilable = false;
  public pastDate = false;
  public sessions = [];
  public slotDuration: any;
  public slotHours = [];
  public slots = [];
  public sideNavData: any;
  public tabChangeEvent: MatTabChangeEvent;
  public tabIndex = 0;
  public unAvailableSlotTm: any;
  public unAvailbleSlots = [];
  public week = ['Sunday', 'Monday', 'Tuesday', 'Wednesday', 'Thursday', 'Friday', 'Saturday'];
  private appointments = [];
  private clickedDateSubscription: Subscription;
  private dayDataSubjectSubscription: Subscription;
  private doctorDataSubjectSubscription: Subscription;
  private isDocAppointmentAvilableSubscription: Subscription;
  private isCancelledSuccefullySubscription: Subscription;
  private orgInfo: any;
  private selectedDoctorData: any;
  private sideNavDataSubscription: Subscription;
  private slotsInfo: any;
  private tabIndexDataSubscription: Subscription;
  private todayDate = new Date();
  docId: any;
  regId: any;

  constructor(
    private userService: UserService,
    private doctorService: DoctorService,
    private appointmentApiService: AppointmentApiService,
    private toastr: ToastrService
  ) {
    this.userSession = this.userService.getUser();
  }
  private userSession: UserSession;

  ngOnInit() {
    this.clickedDate = new Date();
    this.clickedDay = this.week[new Date().getDay()];
    // This subscription for get the sidebar data from doctorList component
    this.sideNavDataSubscription = this.doctorService.sideNavData.subscribe(
      data => {
        if (data) {
          this.sideNavData = data;
        }
      });
  }

  ngAfterViewInit() {
    // This subscription for get the changed date from doctor schedule calander
    this.clickedDateSubscription = this.doctorService.clickedDate.subscribe(
      data => {
        if (data) {
          this.clickedDate = new Date(data);
          const day = this.clickedDate.getDay();
          this.clickedDay = this.week[day];
          this.doctorAppointments(this.clickedDate);
          this.getDoctorSessions(this.clickedDate, this.clickedDay);
          this.getUnavailableSlots(this.clickedDate);
          this.dayAppointmentCount(this.clickedDate);
        }
      });

      // The subscription for get the selected practitioner data
    this.doctorDataSubjectSubscription = this.doctorService.doctorDataSubject.subscribe(
      data => {
        if (data) {
          this.selectedDoctorData = data;
        }
        if (this.selectedDoctorData && this.selectedDoctorData.organisationInfoList
          && this.selectedDoctorData.organisationInfoList.length) {
          this.docId = this.selectedDoctorData.organisationInfoList[0].docId;
          this.regId = this.selectedDoctorData.regId;
          this.getDoctorSessions(this.clickedDate, this.clickedDay);
          this.dayAppointmentCount(this.clickedDate);
        }
      });

      // This subscription for refresh the page when book/edit an appointment
    this.isDocAppointmentAvilableSubscription = this.doctorService.isDocAppointmentAvilable.subscribe(
      val => {
        if (val) {
          this.getDoctorSessions(this.clickedDate, this.clickedDay);
          this.dayAppointmentCount(this.clickedDate);
          this.getUnavailableSlots(this.clickedDate);
          this.doctorAppointments(this.clickedDate);
        }
      });

      // This subscription for refresh the page when cancelled the appointment
    this.isCancelledSuccefullySubscription = this.doctorService.isCancelledSuccefully.subscribe(
      val => {
        if (val) {
          this.getDoctorSessions(this.clickedDate, this.clickedDay);
          this.doctorAppointments(this.clickedDate);
        }
      });

      // This Subscription get the tabindex while refreshing screen
    this.tabIndexDataSubscription = this.doctorService.tabIndexData.subscribe(val => {
      this.tabIndex = val;
    });
  }

  /**
   * @description this method is used to open the book an appointment sidebar
   */
  bookAppointment() {
    this.sideNavData.sideNav.open();
    this.sideNavData.cancelNav.close();
    this.sideNavData.viewNav.close();
    this.sideNavData.completedNav.close();
  }

  /**
   * @description this method is used to open the cancel an appointment sidebar
   */
  cancelAppointment() {
    this.sideNavData.cancelNav.open();
    this.sideNavData.viewNav.close();
    this.sideNavData.sideNav.close();
    this.sideNavData.completedNav.close();
  }

  /**
   * @description this method is used to open the cancelled appointment sidebar
   */
  canceledAppointment() {
    this.cancelledSideNav = true;
    this.cancelSideNav = false;
    const cancelledNavData = {
      cancelledSideNav: this.cancelledSideNav,
      cancelSideNav: this.cancelSideNav
    };
    this.doctorService.cancelledNavData.next(cancelledNavData);
    this.sideNavData.cancelNav.open();
  }

  /**
   * @description this method is used to open the completed appointment sidebar
   */
  completedAppointment() {
    this.sideNavData.cancelNav.close();
    this.sideNavData.viewNav.close();
    this.sideNavData.sideNav.close();
    this.sideNavData.completedNav.open();
  }

  /**
   * @description this method is used to get the appointments and status from API
   * @function fetchDoctorAppointments
   */
  doctorAppointments(date) {
    this.appointments = [];
    const payload = {
      empId: this.docId,
      orgId: this.userSession.orgId,
      searchDt: date
    };
    this.appointmentApiService.fetchDoctorAppointments(payload).subscribe(
      response => {
        if (response && response.responseCode === 'E200') {
          this.docAppointments = response.data;
          if (this.docAppointments && this.docAppointments.slotsInfo) {
            this.slotsInfo = this.docAppointments.slotsInfo;
            if (this.slotsInfo && this.slotsInfo.length) {
              this.appointments = [];
              this.slotsInfo.forEach(element => {
                const appointment = {
                  time: element.appointmentStartTm,
                  status: element.patientStatus,
                  name: element.firstNm + ' ' + element.lastNm,
                  mobileNo: element.mobileNo,
                  hosPatientId: element.hosPatientId,
                  services: element.serviceNm,
                  serviceId: element.serviceId,
                  doctorId: element.doctorId,
                  doctorNm: element.doctorNm,
                  patientId: element.patientId,
                  email: element.email,
                  imagePath: element.imagePath,
                  appointmentId: element.patientAppointmentId
                };
                this.appointments.push(appointment);
                this.selectedSessionHours(event);
              });
            }
          } else {
            this.selectedSessionHours(event);
          }
        } else if (response && response.responseCode === 'E400') {
          this.selectedSessionHours(event);
        }
      });
  }

  /**
  * @description this method is used to get the doctor sessions and slots
  * @function fetchDoctorSettingsInfo
  * @param date
  * @param day
  */
  getDoctorSessions(date, day) {
    const payload = {
      regId: this.regId,
      orgId: this.userSession.orgId,
      empId: this.docId,
      searchDt: date,
      searchDay: day
    };
    this.appointmentApiService.fetchDoctorSettingsInfo(payload)
      .subscribe(response => {
        if (response && response.responseCode === 'E200') {
          this.selectedDoctorData = response.data;
          if (this.selectedDoctorData) {
            this.orgInfo = this.selectedDoctorData.organisationInfoList;
            this.orgInfo.forEach(element => {
              this.doctorEmpId = element.docId;
              this.slotDuration = element.slotDurationFeeInfo.apptDuration ? element.slotDurationFeeInfo.apptDuration : 0;
              if (this.orgInfo && element.docAppointmentSlots && element.docAppointmentSlots.length) {
                this.isSlotsAvilable = false;
                this.doctorService.isSlotAvlDay.next(this.isSlotsAvilable);
                this.doctorAppSlots = element.docAppointmentSlots;
                this.doctorAppSlots.forEach(element1 => {
                  this.doctorSlotTimings = element1.slotTimings;
                  this.sessions = [];
                  this.doctorSlotTimings.forEach((element2, i) => {
                    this.sessions.push('session ' + (i + 1));
                  });
                  this.doctorAppointments(date);
                  this.selectedSessionHours(event);
                });
              } else {
                this.isSlotsAvilable = true;
                this.doctorService.isSlotAvlDay.next(this.isSlotsAvilable);
                this.doctorService.tabIndexData.next(0);
              }
            });
          }
        }
      });
  }

  /**
   * @description this method is used to cheange the slot status and data coming from API
   * @param slot
   */
  getSlot(slot) {
    if (this.unAvailbleSlots && this.unAvailbleSlots.length) {
      const matchedIndex = this.unAvailbleSlots.findIndex(val => {
        return slot.time === val;
      });
      if (matchedIndex !== -1) {
        slot.status = 'UNAVAILABLE';
      }
    }

    if (this.appointments && this.appointments.length) {
      const matchedIindex = this.appointments.findIndex(val => {
        return slot.time === val.time;
      });
      if (matchedIindex !== -1) {
        slot.status = this.appointments[matchedIindex].status;
        slot.name = this.appointments[matchedIindex].name;
        slot.mobileNo = this.appointments[matchedIindex].mobileNo;
        slot.hosPatientId = this.appointments[matchedIindex].hosPatientId;
        slot.services = this.appointments[matchedIindex].services;
        slot.serviceId = this.appointments[matchedIindex].serviceId;
        slot.doctorId = this.appointments[matchedIindex].doctorId;
        slot.doctorNm = this.appointments[matchedIindex].doctorNm;
        slot.patientId = this.appointments[matchedIindex].patientId;
        slot.email = this.appointments[matchedIindex].email;
        slot.imagePath = this.appointments[matchedIindex].imagePath;
        slot.appointmentId = this.appointments[matchedIindex].appointmentId;
        slot.PatientData =
          this.appointments[matchedIindex].name +
          '\n' +
          this.appointments[matchedIindex].hosPatientId +
          '\n' +
          this.appointments[matchedIindex].mobileNo +
          '\n' +
          'services to be provided \n 1)' +
          this.appointments[matchedIindex].services;
      }
    }
    slot.date = this.clickedDate;
  }

  /**
   * @description this method is used to get the unavailable slots from API
   * @function fetchUnavailableSlots
   * @param date
   */
  getUnavailableSlots(date) {
    const payload = {
      searchDt: date,
      empId: this.docId,
      orgId: this.userSession.orgId,
    };

    this.appointmentApiService.fetchUnavailableSlots(payload).subscribe(
      response => {
        if (response && response.responseCode === 'E200') {
          this.unAvailbleSlots = [];
          const data = response.data;
          this.unAvailableSlotTm = data['unAvailableSlotTm'];
          if (this.unAvailableSlotTm && this.unAvailableSlotTm.length) {
            this.unAvailableSlotTm.forEach(ele => {
              const start = ele.slice(0, 5).split(':');
              const end = ele.slice(8, 13).split(':');
              const startInMinutes = start[0] * 60 + start[1] * 1;
              const endInMinutes = end[0] * 60 + end[1] * 1;
              const intervalInMinutes = Number(this.slotDuration);
              const times = [];
              for (let i = startInMinutes; i <= endInMinutes; i += intervalInMinutes) {
                let hour = Math.floor(i / 60) + '';
                let minute = i % 60 + '';
                minute = (minute.length < 2) ? '0' + minute : minute;
                hour = (hour.length < 2) ? '0' + hour : hour;
                times.push(hour + ':' + minute);
              }
              for (let i = 0; i < times.length - 1; i++) {
                const time = times[i];
                this.unAvailbleSlots.push(time);
              }
            });
          }
        } else if (response && response.responseCode === 'E400') {
          this.toastr.error('Error', response.responseMessage);
        }
      });
  }

  /**
   * @description this method is used to open the sidebar based on slot status and time.
   * @param slot
   */
  openAppointmentSlot(slot) {
    let hour = '' + moment().hour();
    const minute = moment().minute();
    this.doctorService.appointmentSlot.next(slot);
    this.doctorService.reBookSlot.next(this.sideNavData.sideNav);
    if (hour.length < 2) {
      hour = '0' + hour;
    }
    const currentTime = hour + ':' + minute;
    const isTimeAvl = currentTime <= slot.time;
    const today = this.todayDate.getDate();
    const slotDate = slot.date.getDate();
    switch (slot.status) {
      case 'UNAVAILABLE':
        this.pastDate = true;
        if (this.pastDate) {
          setTimeout(() => {
            this.pastDate = false;
            this.doctorService.alertMessageStatus.next(this.pastDate);
          }, 1000);
          this.doctorService.alertMessageStatus.next(this.pastDate);
        }
        break;
      case 'AVAILABLE':
        const slotMonth = slot.date.getMonth();
        const currentMonth = new Date().getMonth();
        const slotYear = slot.date.getFullYear();
        const currentYear = new Date().getFullYear();
        if (slotYear === currentYear) {
          if (slotMonth === currentMonth) {
            if (today === slotDate) {
              if (isTimeAvl) {
                this.pastDate = false;
              } else {
                this.pastDate = true;
                if (this.pastDate) {
                  setTimeout(() => {
                    this.pastDate = false;
                    this.doctorService.alertMessageStatus.next(this.pastDate);
                  }, 1000);
                  this.doctorService.alertMessageStatus.next(this.pastDate);
                }
              }
            } else if (today > slotDate) {
              this.pastDate = true;
              if (this.pastDate) {
                setTimeout(() => {
                  this.pastDate = false;
                  this.doctorService.alertMessageStatus.next(this.pastDate);
                }, 1000);
                this.doctorService.alertMessageStatus.next(this.pastDate);
              }
            } else if (today < slotDate) {
              this.pastDate = false;
            }
          } else if (slotMonth < currentMonth) {
            this.pastDate = true;
            if (this.pastDate) {
              setTimeout(() => {
                this.pastDate = false;
                this.doctorService.alertMessageStatus.next(this.pastDate);
              }, 1000);
              this.doctorService.alertMessageStatus.next(this.pastDate);
            }
          } else if (slotMonth > currentMonth) {
            this.bookAppointment();
          }
        } else if (slotYear < currentYear) {
          this.pastDate = true;
          if (this.pastDate) {
            setTimeout(() => {
              this.pastDate = false;
              this.doctorService.alertMessageStatus.next(this.pastDate);
            }, 1000);
            this.doctorService.alertMessageStatus.next(this.pastDate);
          }
        } else if (slotYear > currentYear) {
          this.bookAppointment();
        }
        break;
      case 'ACTIVE':
        this.viewAppointment();
        break;
      case 'COMPLETED':
        this.completedAppointment();
        break;
      default:
        break;
    }
  }

  /**
   * @description this method is used to get the hours based clicking sessions in mat-tabs.
   * @param tabChangeEvent
   */
  selectedSessionHours(tabChangeEvent) {
    if (this.doctorSlotTimings && this.doctorSlotTimings.length) {
      tabChangeEvent.index = tabChangeEvent.index;
      if (tabChangeEvent.index === undefined || (!tabChangeEvent.index && tabChangeEvent.index !== 0)) {
        tabChangeEvent.index = this.tabIndex;
      } else {
        this.tabIndex = tabChangeEvent.index;
      }
      this.doctorService.tabIndexData.next(tabChangeEvent.index);
      this.slotHours = [];
      this.slots = [];
      const start = this.doctorSlotTimings[tabChangeEvent.index].fromTm.split(':');
      const end = this.doctorSlotTimings[tabChangeEvent.index].toTm.split(':');
      const statrtInMinutes = start[0] * 60 + start[1] * 1;
      const endInMinutes = end[0] * 60 + end[1] * 1;
      const intervalMinutes = 60;
      for (let i = statrtInMinutes; i < endInMinutes; i += intervalMinutes) {
        let hour = Math.floor(i / 60) + '';
        hour = hour.length < 2 ? '0' + hour : hour;
       const hourMinutes = this.addMinutesToHour((i / 60));
        let nextHour = Math.floor((i + 60) / 60) + '';
        nextHour = nextHour.length < 2 ? '0' + nextHour : nextHour;
        let slotEndMinutes = Math.floor((i + 60) / 60) * 60;
        if (((i + 60) / 60) % 1) {
          slotEndMinutes =  (Math.floor((i + 60) / 60) * 60) + 30;
        }
        const isEndTimeMax = endInMinutes < slotEndMinutes ? true : false;
       const nextHourMinutes = this.addMinutesToHour(isEndTimeMax ? nextHour :  (i + 60) / 60);
        this.slotHours.push({ hour: hour, slots: [], nextHour: nextHour, hourMinutes: hourMinutes, nextHourMinutes: nextHourMinutes });
      }
      if (this.slotHours && this.slotHours.length > 0) {
        this.slotHours.forEach(val => {
          this.selectedSessionSlots(val);
        });
      }
    }
  }

  addMinutesToHour(hour) {
    let minutes = '00';
    if (Number(hour) % 1) {
      minutes = '30';
    }
    return minutes;
  }
  /**
   * @description this method is used to get the slots based on hour.
   * @param hourObj
   */
  selectedSessionSlots(hourObj) {
    hourObj.slots = [];
    const statrtInMinutes = ((hourObj.hour * 60) + Number(hourObj.hourMinutes));
    const endInMinutes = ((hourObj.nextHour * 60) + Number(hourObj.nextHourMinutes));
    const slotDuration = Number(this.slotDuration);
    for (let i = statrtInMinutes; i < endInMinutes; i += slotDuration) {
      let hour = Math.floor(i / 60) + '';
      let minute = (i % 60) + '';
      minute = minute.length < 2 ? '0' + minute : minute;
      hour = hour.length < 2 ? '0' + hour : hour;
      hourObj.slots.push({
        time: hour + ':' + minute,
        status: 'AVAILABLE',
        name: '',
        mobileNo: '',
        hosPatientId: '',
        services: '',
        date: '',
        serviceId: '',
        doctorId: '',
        doctorNm: '',
        patientId: '',
        email: '',
        imagePath: '',
        patientData: '',
        appointmentId: ''
      });
    }
    if (hourObj.slots && hourObj.slots.length) {
      hourObj.slots.forEach(val => {
        this.getSlot(val);
      });
    }
  }

  /**
   * @description this method is used to open the view an appointment sidebar
   */
  viewAppointment() {
    this.sideNavData.viewNav.open();
    this.sideNavData.cancelNav.close();
    this.sideNavData.completedNav.close();
    this.sideNavData.sideNav.close();
  }

  /**
   * @description This method to get the appointment count for the day from API
   * @function fetchMonthWiseAppointments
   * @param date
   */
  dayAppointmentCount(date) {
    const payload = {
      orgId: this.userSession.orgId,
      docId: this.docId,
      monthStartDt: date,
      monthEndDt: date

    };
    this.appointmentApiService.fetchMonthWiseAppointments<any>(payload).subscribe(
      response => {
        if (response.responseCode === 'E200') {
          this.doctorService.countFromDay.next(response.data);
        } else if (response.responseCode === 'E400') {
          this.toastr.error('Failure!', response.responseMessage);
        }
      },
      () => {
        this.toastr.error('Failure!', 'Something went wrong');
      }
    );
  }

  ngOnDestroy() { }
}
