import { Component, OnInit, OnDestroy } from '@angular/core';
import { Input, Inject, ChangeDetectorRef, AfterViewInit } from '@angular/core';
import { CalendarEvent } from 'angular-calendar';
import { ToastrService } from 'ngx-toastr';
import { UserService, MOMENT } from '@core/services';
import { UserSession } from '@core/models/user-session';
import { DoctorService } from '@app/modules/appointment/services/doctor.service';
import { ActivatedRoute } from '@angular/router';
import { FormGroup } from '@angular/forms';
import { AppointmentApiService } from '@core/services/appointment-api.service';
import { ReportsService } from '@core/services/reports-api.service';
import { DatePipe } from '@angular/common';
import { Subscription } from 'rxjs';
import { AutoUnsubscribe } from 'ngx-auto-unsubscribe';
@AutoUnsubscribe()
@Component({
  selector: 'app-calendar',
  templateUrl: './calendar.component.html',
  styleUrls: ['./calendar.component.scss']
})
export class CalendarComponent implements OnInit, AfterViewInit, OnDestroy {
  @Input()
  value: any;
  view = 'day';
  public viewDate;
  events: CalendarEvent<{ total: number; status: string }>[] = [];
  doctorSlots: any;
  doctorResult: any;
  doctorName: any;
  mobileNumber: any;
  empId: any;
  public j;
  public isSlotsAvilable = false;
  completedAppointmentsCount: any;
  date = [];
  public newDate = new Date();
  public changedMothDate: any;
  doctorAppointments: any;
  public changedDate: any;
  public changedMonthDate: any;
  public monthWiseCancelledAppointmentsCount;
  public monthWiseCompletedAppointmentsCount;
  public monthWiseShceduledAppointmentsCount;
  public monthDoctorAppointments;
  public monthStartDt;
  public monthEndDt;
  private sessionData: UserSession;
  public isCalanderHeader = false;
  doctorData: any;
  orgInfo: any;
  completedAppointmentsCountDay: any;
  scheduledAppointmentsCountDay: any;
  cancelledAppointmentsCountDay: any;
  weekWiseCancelledAppointmentsCount: any;
  weekWiseCompletedAppointmentsCount: any;
  weekWiseShceduledAppointmentsCount: any;
  monthKey: 'month';
  endDateOfWeek: any;
  startDateOfWeek: any;
  countWeek = [];
  calendarDataSubscription: Subscription;
  doctorDataSubjectSubscription: Subscription;
  weekDateSubscription: Subscription;
  isSlotsAvilableSubscription: Subscription;
  viewDataSubscription: Subscription;
  doctorResultSubscription: Subscription;
  datesFromWeekSubscription: Subscription;
  monthDataSubscription: Subscription;
  monthDataFromDocScheduleSubscription: Subscription;
  // eventData: [];

  constructor(
    @Inject(MOMENT) private moment,
    private userService: UserService,
    private changeDetector: ChangeDetectorRef,
    private toastr: ToastrService,
    private appointmentService: AppointmentApiService,
    private doctorService: DoctorService,
    private route: ActivatedRoute,
    private reportsService: ReportsService,
    private datePipe: DatePipe
  ) {
    this.userSession = this.userService.getUser();
  }
  private userSession: UserSession;

  ngOnInit() {
    this.calendarDataSubscription = this.doctorService.calendarData.subscribe(
      data => {
        if (data) {
          this.viewDate = data;
        }
      }
    );

    this.isSlotsAvilableSubscription = this.doctorService.isSlotAvlDay.subscribe(
      val => {
        this.isSlotsAvilable = val;
      }
    );

    this.viewDataSubscription = this.doctorService.viewData.subscribe(val => {
      if (val === 'week' || val === 'month') {
        this.isSlotsAvilable = false;
      }
    });
  }

  ngAfterViewInit() {
    this.doctorService.doctorDataSubject.subscribe(data => {
      if (data) {
        this.doctorData = data;
        if (
          this.doctorData &&
          this.doctorData.organisationInfoList &&
          this.doctorData.organisationInfoList.length
        ) {
          this.orgInfo = this.doctorData.organisationInfoList;
          this.empId = this.orgInfo[0].docId;
          this.doctorAppointment();
        }
      }
    });

    this.monthDataSubscription = this.doctorService.monthData.subscribe(
      data => {
        if (data) {
          this.changedMonthDate = new Date(data);
          this.getMonthStartEndDate(this.changedMonthDate);
        }
      }
    );
    this.monthDataSubscription = this.doctorService.monthData.subscribe(
      data => {
        if (data) {
          this.changedMonthDate = new Date(data);
          this.getMonthStartEndDate(this.changedMonthDate);
        }
      }
    );
    this.monthDataFromDocScheduleSubscription = this.doctorService.monthDataFromDocSchedule.subscribe(
      val => {
        if (val) {
          this.changedDate = new Date(val);
          this.getMonthStartEndDate(this.changedDate);
        }
      }
    );

    this.datesFromWeekSubscription = this.doctorService.datesFromWeek.subscribe(
      data => {
        if (data) {
          this.startDateOfWeek = this.datePipe.transform(
            data[0].date,
            'yyyy-MM-dd'
          );
        }
      }
    );
    this.datesFromWeekSubscription = this.doctorService.datesFromWeek.subscribe(
      data => {
        if (data) {
          this.endDateOfWeek = this.datePipe.transform(
            data[6].date,
            'yyyy-MM-dd'
          );
        }
      }
    );

    this.doctorService.countFromDay.subscribe(val => {
      if (val) {
        this.completedAppointmentsCountDay =
          val[0].monthWiseCompletedAppointmentsCount;
        this.scheduledAppointmentsCountDay =
          val[0].monthWiseShceduledAppointmentsCount;
        this.cancelledAppointmentsCountDay =
          val[0].monthWiseCancelledAppointmentsCount;
      }
    });

    this.doctorService.countFromWeek.subscribe(val => {
      if (val) {
        this.weekWiseCancelledAppointmentsCount =
          val.data[0].monthWiseCancelledAppointmentsCount;
        this.weekWiseCompletedAppointmentsCount =
          val.data[0].monthWiseCompletedAppointmentsCount;
        this.weekWiseShceduledAppointmentsCount =
          val.data[0].monthWiseShceduledAppointmentsCount;
      }
    });
  }

  /**
   * @description This method is used to get rhe month start and end dates
   */
  getMonthStartEndDate(date: Date) {
    this.monthStartDt = this.moment(date)
      .startOf('month')
      .format('YYYY-MM-DD');
    this.monthEndDt = this.moment(date)
      .endOf('month')
      .format('YYYY-MM-DD');
    this.doctorAppointment();
  }

  /**
   * @description This method is used to fetch the month Appointments for a particular doctor
   */
  doctorAppointment() {
    const payload = {
      orgId: this.userSession.orgId,
      docId: this.empId,
      monthStartDt: this.monthStartDt,
      monthEndDt: this.monthEndDt
    };
    this.appointmentService.fetchMonthWiseAppointments<any>(payload).subscribe(
      response => {
        if (response && response.responseCode === 'E200') {
          const events = response.data.map(obj => {
            return {
              title: obj.title,
              start: new Date(obj.start),
              end: new Date(obj.end),
              allDay: true,
              meta: {
                total: obj.meta.total,
                status: obj.meta.status
              }
            };
          });
          this.events = events;
          this.doctorAppointments = response.data;
          this.monthWiseCancelledAppointmentsCount = this.doctorAppointments[0].monthWiseCancelledAppointmentsCount;
          this.monthWiseCompletedAppointmentsCount = this.doctorAppointments[0].monthWiseCompletedAppointmentsCount;
          this.monthWiseShceduledAppointmentsCount = this.doctorAppointments[0].monthWiseShceduledAppointmentsCount;
          this.changeDetector.detectChanges();
        } else if (response && response.responseCode === 'E400') {
          this.toastr.error('Failure!', response.responseMessage);
        }
      },
      () => {
        this.toastr.error('Failure!', 'Something went wrong');
      }
    );
  }

  /**
   * @description This method is used generate the reports for particular doctor (monthWise)
   */
  monthAppointmentListReport() {
    const queryparams = {
      key: 'month',
      doctorId: this.empId,
      fromDate: this.monthStartDt,
      toDate: this.monthEndDt,
      orgId: this.userSession.orgId,
      orgGrpId: this.userSession.orgGroupId
    };
    this.reportsService
      .fetchDoctorWiseAppointsBasedOnDayWeekMonth(queryparams)
      .subscribe(
        response => {
          console.log('responseData', response);
          if (response instanceof ArrayBuffer) {
            console.log('responseData', response);
            const appointmentReport = new Blob([response], {
              type: 'application/pdf'
            });
            const reportURL = URL.createObjectURL(appointmentReport);
            const win = window.open('', '_blank');
            win.location.href = reportURL;
            setTimeout(() => {
              win.print();
            }, 1000);
          }
        },
        () => {
          this.toastr.error('Failure!', 'Something went wrong');
        }
      );
  }

  /**
   * @description This method is used generate the reports for particular doctor (weekWise)
   */
  weekAppointmentListReport() {
    const queryparams = {
      key: 'week',
      doctorId: this.empId,
      fromDate: this.startDateOfWeek,
      orgId: this.userSession.orgId,
      orgGrpId: this.userSession.orgGroupId,
      toDate: this.endDateOfWeek
    };
    this.reportsService
      .fetchDoctorWiseAppointsBasedOnDayWeekMonth(queryparams)
      .subscribe(
        response => {
          console.log('responseData', response);
          if (response instanceof ArrayBuffer) {
            console.log('responseData23', response);
            const appointmentReport = new Blob([response], {
              type: 'application/pdf'
            });
            const reportURL = URL.createObjectURL(appointmentReport);
            const win = window.open('', '_blank');
            win.location.href = reportURL;
            setTimeout(() => {
              win.print();
            }, 1000);
            console.log(response);
          }
        },
        () => {
          this.toastr.error('Failure!', 'Something went wrong');
        }
      );
  }

  /**
   * @description This method is used generate the reports for particular doctor (dayWise)
   */
  dayAppointmentListReport() {
    const queryparams = {
      key: 'day',
      doctorId: this.empId,
      fromDate: this.datePipe.transform(this.viewDate, 'yyy-MM-dd'),
      toDate: this.datePipe.transform(this.viewDate, 'yyy-MM-dd'),
      orgId: this.userSession.orgId,
      orgGrpId: this.userSession.orgGroupId
    };
    this.reportsService
      .fetchDoctorWiseAppointsBasedOnDayWeekMonth(queryparams)
      .subscribe(
        response => {
          console.log('responseData', response);
          if (response instanceof ArrayBuffer) {
            /**
             * @description this condition is written based on the buffer value
             */
            if (response.byteLength === 184) {
              this.toastr.warning('No Records Found');
            } else {
              console.log('responseData', response);
              const appointmentReport = new Blob([response], {
                type: 'application/pdf'
              });
              const reportURL = URL.createObjectURL(appointmentReport);
              const win = window.open('', '_blank');
              win.location.href = reportURL;
              setTimeout(() => {
                win.print();
              }, 1000);
            }
          }
        },
        () => {
          this.toastr.error('Failure!', 'Something went wrong');
        }
      );
  }
  ngOnDestroy() {}
}
