import { Component, OnInit, Input, OnDestroy } from '@angular/core';
import { MatSidenav } from '@angular/material';
import { DoctorService } from '../../services/doctor.service';
import { Subscription } from 'rxjs';
import { AutoUnsubscribe } from 'ngx-auto-unsubscribe';
@AutoUnsubscribe()
@Component({
    selector: 'app-cancelled-appointment',
    templateUrl: './cancelled-appointment.component.html',
    styleUrls: ['./cancelled-appointment.component.scss']
})
export class CancelledAppointmentComponent implements OnInit, OnDestroy {

    @Input()
    sideNavRef: MatSidenav;
    public slotData: any;
    public sideNav: any;
    private appointmentSlotSubscription: Subscription;
    private reBookSlotSubscription: Subscription;

    constructor(
        private doctorService: DoctorService
    ) { }

    ngOnInit() {
        this.appointmentSlotSubscription = this.doctorService.appointmentSlot
            .subscribe((data) => {
                this.slotData = data;
            });

        this.reBookSlotSubscription = this.doctorService.reBookSlot
            .subscribe((data) => {
                this.sideNav = data;
            });
    }

    /**
     * @description This method is used to Close the sidebar
     */
    closeSideNav() {
        this.sideNavRef.close();
    }
    /**
    * @description This method is used to Close the cancelled sidebar and open the book an appointment sidebar
    */
    reBook() {
        this.sideNavRef.close();
        this.sideNav.open();
        this.doctorService.cancelledAppointmentData.next(this.slotData);
    }

    ngOnDestroy() { }
}
