import { Component, OnInit, ViewEncapsulation, ViewChild, OnDestroy } from '@angular/core';
import { UserSession } from '@core/models/user-session';
import { UserService } from '@core/services/user.service';
import { AppointmentApiService } from '@core/services/appointment-api.service';
import { DoctorService } from '../../services/doctor.service';
import { MatSidenav, MatDatepicker } from '@angular/material';
import { ToastrService } from 'ngx-toastr';
import { RegisterationBillingService } from '@core/services';
import * as moment from 'moment';
import { Subscription } from 'rxjs';
import { AutoUnsubscribe } from 'ngx-auto-unsubscribe';
@AutoUnsubscribe()
@Component({
  selector: 'app-doctorslist',
  templateUrl: './doctorslist.component.html',
  styleUrls: ['./doctorslist.component.scss'],
  encapsulation: ViewEncapsulation.None
})
export class DoctorslistComponent implements OnInit, OnDestroy {
  @ViewChild('sidenav')
  sidenav: MatSidenav;

  @ViewChild('completedNav')
  completedNav: MatSidenav;

  @ViewChild('viewNav')
  viewNav: MatSidenav;

  @ViewChild('cancelNav')
  cancelNav: MatSidenav;

  public cancelledNavData: any;
  public cancelSideNav = true;
  public cancelledSideNav = false;
  public doctorHeading = false;
  public dayAppointment = false;
  public doctorResult: any;
  public history: any;
  public individualPtCancel = false;
  public isAppSchedule = true;
  public isAppHistory = false;
  public specialisationHeading = true;
  public selectedDoctorName: any;
  public specialization = [];
  public selectedService: any;
  public services: any;
  public searchDate = '';
  public patientId = '';
  public patientNm = '';
  public mobileNo = '';
  public serviceNm = '';
  public patientStatus = '';
  public today = new Date();
  public startDate = this.today;
  public endDate = this.today;
  public pastDate: any;
  private cancelledNavDataSubscription: Subscription;
  private isDocAppointmentAvilableSubscription: Subscription;
  private selectedDoctorData: any;
  private userSession: UserSession;
  pastDateWeek: any;
  orgInfo: any;
  docAppointments: any;
  isDocAppointmentAvilable = false;

  constructor(
    private appointmentApiService: AppointmentApiService,
    private userService: UserService,
    private doctorService: DoctorService,
    private toastr: ToastrService,
    private registrationBIllingService: RegisterationBillingService
  ) {
    this.userSession = userService.getUser();
  }
  ngOnInit() {
    this.doctorService.alertMessageStatus.subscribe((data) => {
      this.pastDate = data;
    });
    // this.doctorService.alertMessageStatusFromWeek.subscribe( (data) => {
    //   this.pastDateWeek = data;
    // });
    this.isAppSchedule = true;
    this.isAppHistory = false;
    this.doctorService.cancelledNavData.subscribe((data) => {
      this.cancelledNavData = data;
      if (this.cancelledNavData) {
        this.cancelSideNav = this.cancelledNavData.cancelSideNav;
        this.cancelledSideNav = this.cancelledNavData.cancelledSideNav;
        this.individualPtCancel = this.cancelledNavData.individualPtCancel;
      }
    });

    const sideNavData = {
      sideNav: this.sidenav,
      completedNav: this.completedNav,
      viewNav: this.viewNav,
      cancelNav: this.cancelNav
    };
    this.doctorService.sideNavData.next(sideNavData);

    this.getDoctorServices();
    const payload = {
      orgId: this.userSession.orgId,
      regId: this.userSession.regId
    };
    this.appointmentApiService.fetchListOfSpecialities(payload).subscribe(
      (response: any) => {
        if (response.responseCode === 'E200') {
          this.specialization = response.data;
        } else if (response.responseCode === 'E400') {
          this.toastr.error('Error', response.responseMessage);
        }
      },
      err => {
        this.toastr.error('Something went wrong');
      });

    this.isDocAppointmentAvilableSubscription = this.doctorService.isDocAppointmentAvilable.subscribe(val => {
      if (val) {
        this.isDocAppointmentAvilable = val;
      }
    });
  }


  appHistory() {
    this.isAppSchedule = false;
    this.isAppHistory = true;
    this.getAppointmentHistory();
  }

  appSchedule() {
    this.doctorService.doctorDataSubject.next(this.selectedDoctorData);
    this.isAppSchedule = true;
    this.isAppHistory = false;
  }

  /**
   * @description this method is used for  open the book an appointment mat sidenav.
   */
  bookAppointment() {
    this.sidenav.open();
    this.cancelNav.close();
  }

  /**
   * @description this method is used for  open the cancel an appointment mat sidenav.
   */
  cancelAppointment() {
    this.cancelSideNav = true;
    this.cancelledSideNav = false;
    this.individualPtCancel = false;
    this.cancelNav.open();
    this.sidenav.close();
  }

  /**
   * @description this method is used for open and close the mat date picker.
   */
  datePickerToggler(datePicker: MatDatepicker<Date>) {
    datePicker[datePicker.opened ? 'close' : 'open']();
  }

  /**
  * @description this method is used for get doctors in organaization from API.
  * @function fetchDoctorsInOrg
  */
  doctorsMenu(selected) {
    this.isAppSchedule = true;
    this.isAppHistory = false;
    const payload = {
      orgId: this.userSession.orgId,
      specalityList: [selected.specialityId]
    };
    this.appointmentApiService.fetchDoctorsInOrg(payload).subscribe(
      (response: any) => {
        if (response.responseCode === 'E200') {

          this.doctorResult = response.data;
          this.specialisationHeading = false;
          this.doctorHeading = true;
          this.selectedDoctorName = null;
          this.doctorService.doctorResult.next(this.doctorResult);
        } else if (response.responseCode === 'E400') {
        }
      },
      err => { }
    );
  }

  /**
   * @description this method is used for get doctor appointment history in organaization from API.
   * @function getAppointmentHistory
   */
  getAppointmentHistory() {
    const payload = {
      orgId: this.userSession.orgId,
      authenticatedUserId: this.userSession.regId,
      orgGroupId: this.userSession.orgGroupId,
      doctorId: this.selectedDoctorName.empId,
      fromDt: this.startDate,
      toDt: this.endDate,
      serviceId: this.selectedService
    };
    this.appointmentApiService.fetchAppointmentHistory(payload).subscribe(
      response => {
        if (response && response.responseCode === 'E200') {
          this.history = response.data;
        } else if (response && response.responseCode === 'E400') {

        }
      }
    );
  }

  getDoctorServices() {
    const payload = {
      orgId: this.userSession.orgId,
      authenticatedUserId: this.userSession.regId,
      orgGroupId: this.userSession.orgGroupId
    };
    this.registrationBIllingService.servicesList(payload).subscribe(
      response => {
        if (response && response.responseCode === 'E200') {
          this.services = response.data;
        }
      }
    );
  }

  /**
  * @description this method is used for get selected doctor data from API.
  * @function fetchDoctorSettingsInfo
  */
  selectedDoctor(value) {
    this.isAppSchedule = true;
    this.isAppHistory = false;
    this.selectedDoctorName = value;
    this.doctorService.doctorData.next(this.selectedDoctorName);
    const payload = {
      empId: value.empId,
      orgId: this.userSession.orgId,
      regId: this.userSession.regId
    };
    this.appointmentApiService.fetchDoctorSettingsInfo(payload).subscribe((response) => {
      if (response.responseCode === 'E200') {
        this.selectedDoctorData = response.data;
        this.orgInfo = this.selectedDoctorData.organisationInfoList;
        this.docAppointments = this.orgInfo[0].docAppointmentSlots;
        this.doctorService.tabIndexData.next(0);
        if (this.docAppointments.length) {
          this.isDocAppointmentAvilable = true;
        } else {
          this.isDocAppointmentAvilable = false;
        }
        this.doctorHeading = false;
        this.doctorService.doctorDataSubject.next(this.selectedDoctorData);
      } else if (response.responseCode === 'E400') {
        this.toastr.error('error', response.responseMessage);
      }
    });
  }

  ngOnDestroy() { }
}
