import {
  Component,
  OnInit,
  Input,
  Inject,
  AfterViewInit,
  OnDestroy
} from '@angular/core';
import { MatSidenav } from '@angular/material/sidenav';
import { ToastrService } from 'ngx-toastr';
import { MatDatepicker } from '@angular/material/datepicker';
import { UserSession } from '@core/models/user-session';
import { UserService, CLONEDEEP } from '@core/services';
import { RegisterationBillingService, UtilService } from '@core/services';
import {
  FormBuilder,
  FormGroup,
  Validators,
  FormControl,
  AbstractControl
} from '@angular/forms';
import { DoctorService } from '../../services/doctor.service';
import { DatePipe } from '@angular/common';
import { AutoUnsubscribe } from 'ngx-auto-unsubscribe';
import { Subscription } from 'rxjs';
import { AppointmentApiService } from '@core/services/appointment-api.service';
@AutoUnsubscribe()
@Component({
  selector: 'app-bookanappointmentsidenav',
  templateUrl: './bookanappointmentsidenav.component.html',
  styleUrls: ['./bookanappointmentsidenav.component.scss']
})
export class BookanappointmentsidenavComponent
  implements OnInit, AfterViewInit, OnDestroy {
  public serviceList: any;
  public slots = [];
  private doctorData: any;
  public selectedPatient: any;
  public sessions = [];
  public selectedPatientDetails: any;
  public hideData = true;
  public index;
  public visit;
  public modifiedDate;
  public radioButtons = true;
  public displayData = false;
  public minDate = new Date();
  public hospitalPatientId;
  public viewAndEditAppointment;
  public completedStatus = false;
  public completeAppointmentData;
  public cancelledAppointmentData;
  public selectedSlotsData;
  public selectedSlot;
  public filteredDataShow = false;
  public filteredDataHide = false;
  public completedData = false;
  public searchData = true;
  public noSearchData = false;
  public week = [
    'Sunday',
    'Monday',
    'Tuesday',
    'Wednesday',
    'Thursday',
    'Friday',
    'Saturday'
  ];
  bookSideNav: FormGroup;
  @Input()
  slotDuration: any;
  @Input()
  sideNavRef: MatSidenav;
  d = new Date();
  name = this.utilService.generateUniqueId();
  patientList: any;
  selectedDate: any;
  patientSearchData: any;
  patinetSearchControl: any;
  patientData: any;
  searchControl: AbstractControl;
  placeholderValue: string;
  doctorResult: any;
  empId: any;
  orgInfo: any;
  doctorAppSlot: any;
  serviceName: any;
  serviceId: any;
  mobileNumber: any;
  email: any;
  patientId: any;
  patientName: string;
  search: any;
  doctorSlotTimings: any;
  selectedSlotHours: string;
  visitDttm: any;
  fullTime: string;
  filteredData = [];
  visitDay: any;
  visitDate: string;
  doctorName: string;
  cancelNav: any;
  public cancelledSideNav = false;
  public cancelSideNav = false;
  public individualPtCancel = false;
  constructor(
    private userService: UserService,
    private utilService: UtilService,
    private toastr: ToastrService,
    private doctorService: DoctorService,
    private registerationBillingSerivice: RegisterationBillingService,
    private appointmentService: AppointmentApiService,
    private bookFormBuilder: FormBuilder,
    private datePipe: DatePipe,
    @Inject(CLONEDEEP) private cloner
  ) {
    this.userSession = this.userService.getUser();
  }
  private userSession: UserSession;
  closeSideNav() {
    this.sideNavRef.close();
    this.bookSideNav.reset();
    this.selectedPatientDetails = null;
    this.patientList = null;
    this.patientData = null;
    this.displayData = false;
    this.radioButtons = true;
    this.filteredData = [];
    this.filteredDataShow = false;
    this.filteredDataHide = false;
    this.searchData = true;
    this.noSearchData = false;
    this.completedStatus = false;
  }

  ngOnInit() {
    this.initForm();
    this.searchControl = new FormControl('M');
    this.placeholderValue = 'Enter Your Mobile No';
    this.search = 'M';
    this.searchControl.valueChanges.subscribe(value => {
      this.search = value;
      this.bookSideNav.patchValue({ patientSearchData: null });
      switch (this.search) {
        case 'P':
          this.placeholderValue = 'Enter Your Name';
          break;
        case 'A':
          this.placeholderValue = 'Enter Your aadhar No';
          break;
        case 'R':
          this.placeholderValue = 'Enter Your Patient Id';
          break;
        default:
          this.placeholderValue = 'Enter Your Mobile No ';
          break;
      }
    });
    this.doctorService.bookEditApp.next(this.sideNavRef);
    this.servicesList();
    this.doctorService.completeAppointmentData.subscribe(data => {
      if (data) {
        this.completeAppointmentData = data;
        this.rebookPatientDetails(data);
        this.completedStatus = true;
      }
    });
    this.doctorService.sideNavData.subscribe(data => {
      this.cancelNav = data.cancelNav;
    });
    this.doctorService.doctorData.subscribe(data => {
      if (data) {
        this.doctorName = data.doctorName;
      }
    });
  }
  /**
   * @description This method is used to declare list of form control names with validation in a bookSideNav.
   */
  ngAfterViewInit() {
    this.doctorService.doctorDataSubject.subscribe(data => {
      this.doctorData = data;

      if (
        this.doctorData &&
        this.doctorData.organisationInfoList &&
        this.doctorData.organisationInfoList.length
      ) {
        this.orgInfo = this.doctorData.organisationInfoList;
        this.empId = this.orgInfo[0].docId;
      }
    });
  }
  initForm() {
    this.bookSideNav = this.bookFormBuilder.group({
      patientSearchData: '',
      fromDt: '',
      service: ['', Validators.required]
    });
    this.bookSideNav.get('service').valueChanges.subscribe(data => {
      if (data) {
        this.serviceName = data.serviceName;
        this.serviceId = data.serviceId;
      }
    });

    this.bookSideNav.get('patientSearchData').valueChanges.subscribe(data => {
      if (data) {
        const patientSearchData = this.bookSideNav.get('patientSearchData')
          .value;
        this.patientSearchData = patientSearchData;
      }
    });
    this.bookSideNav.get('fromDt').valueChanges.subscribe(data => {
      this.sessions = [];
      const fromDt = this.bookSideNav.get('fromDt').value;
      if (fromDt) {
        const fromDay = fromDt.getDay();
        this.visit = this.datePipe.transform(fromDt, 'yyyy-MM-dd:HH:mm');
        const date = this.visit;
        this.visitDate = this.datePipe.transform(fromDt, 'yyyy-MM-dd');
        this.visitDay = this.week[fromDay];

        this.doctorsAvailableSlots();
        this.selectedDate = date.substring(0, 10);
        const time = this.visit;
        const startTime = time.substring(11, 13);
        const end = 1;
        const endTime = +startTime + end;
        this.fullTime = startTime + ' - ' + endTime;
        this.selectedSlotHours = time.substring(11, 13);
      }
    });

  }
  /**
   * @description This method is used to  search the patient based on patientId,Name,Aadhaar,Mobileno.
   */
  Patientsearch(search, patientSearchData) {
    this.patientList = [];
    const payload = {
      orgId: this.userSession.orgId,
      orgGrpId: this.userSession.orgGroupId,
      authenticatedUserId: this.userSession.regId,
      searchType: this.search,
      patientNm: this.patientSearchData
    };
    this.appointmentService
      .searchPatient(payload)
      .subscribe((response: any) => {
        if (response.responseCode === 'E200') {
          this.patientList = response.data;
          if (this.patientList.length) {
            this.bookSideNav.patchValue({ patientSearchData: null });
            this.radioButtons = true;
            this.displayData = false;
            this.searchData = false;
            this.noSearchData = false;
          } else {
            this.noSearchData = true;
            this.searchData = false;
          }
        } else if (response.response === 'E400') {
          this.toastr.error('Failure!', response.responseMessage);
        }
      });
  }
  /**
   * @description This method is used to call the servicelist in the dropdown.
   */
  servicesList() {
    const payload = {
      orgId: this.userSession.orgId,
      authenticatedUserId: this.userSession.regId,
      orgGroupId: this.userSession.orgGroupId
    };
    this.registerationBillingSerivice.servicesList<any>(payload).subscribe(
      response => {
        if (response.responseCode === 'E200') {
          this.serviceList = response.data;
        } else if (response.responseCode === 'E400') {
          this.toastr.error('Failure!', response.responseMessage);
        }
      },
      () => {
        this.toastr.error('Failure!', 'Something went wrong');
      }
    );
  }
  /**
   * @description This method is used to display the selected patient from the radio buttons.
   */
  PatientDetails(patientData) {
    this.selectedPatientDetails = patientData;
    this.hospitalPatientId = this.selectedPatientDetails.hospitalPatientId;
    this.patientId = this.selectedPatientDetails.patientId;
    this.email = this.selectedPatientDetails.email;
    this.mobileNumber = this.selectedPatientDetails.mobileNumber;
    this.patientName =
      this.selectedPatientDetails.firstName +
      '' +
      this.selectedPatientDetails.lastName;
    this.serviceName = this.selectedPatientDetails.serviceNm || '';
    this.serviceId = this.selectedPatientDetails.serviceId + '';
    this.radioButtons = false;
    this.displayData = true;
    if (this.selectedPatientDetails.appointmentDttm) {
      this.visitDttm = this.selectedPatientDetails.appointmentDttm;
      console.log(this.datePipe.transform(this.visitDttm, 'yyyy-MM-dd: HH:mm'));
    }

  }
  /**
   * @description This method is used to get the data from Rebook appointment screen
   */
  rebookPatientDetails(completeAppointmentData) {
    this.hospitalPatientId = completeAppointmentData.hosPatientId;
    this.patientId = completeAppointmentData.patientId;
    this.email = completeAppointmentData.email;
    this.mobileNumber = completeAppointmentData.mobileNo;
    this.patientName = completeAppointmentData.name;
  }

  /**
   * @description This method is used to bookanAppointment for the Patient.
   */
  bookAppointment() {
    const payload = {
      refDoctorNm: this.doctorName,
      isUpdate: false,
      doctorId: this.empId,
      hosPatientId: this.hospitalPatientId,
      orgGrpId: this.userSession.orgGroupId,
      orgId: this.userSession.orgId,
      patientId: this.patientId,
      patientMailId: this.email,
      patientMobile: this.mobileNumber,
      patientNm: this.patientName,
      serviceId: this.serviceId,
      serviceNm: this.serviceName,
      visitDttm:
        this.selectedDate + ' ' + this.selectedSlotHours + ':00' + ':00'
    };
    this.appointmentService.bookAppointment(payload).subscribe(
      (response: any) => {
        if (response.responseCode === 'E200') {
          this.toastr.success('Success!', response.responseMessage);
          this.searchData = true;
          this.bookSideNav.reset();
          this.selectedPatientDetails = null;
          this.patientList = null;
          this.patientData = null;
          this.displayData = false;
          this.radioButtons = true;
          this.filteredData = [];
          this.filteredDataShow = false;
          this.filteredDataHide = false;
          if (this.completedData) {
            this.closeSideNav();
          }
          this.doctorService.isDocAppointmentAvilable.next(true);
          this.searchData = true;
          this.noSearchData = false;
          this.completeAppointmentData = '';
          this.completedStatus = false;
        } else if (response.response === 'E400') {
          this.toastr.error('Failure!', response.responseMessage);
          this.doctorService.isDocAppointmentAvilable.next(false);
        }
      },
      () => {
        this.toastr.error('Failure!', 'Something went wrong');
      }
    );
  }
  /**
   * @description This method is used to calculate the slots based on the doctorSelected.
   */
  slotsData() {
    this.slots = [];
    this.sessions.forEach(element => {
      const start = element.fromTm.split(':');
      const end = element.toTm.split(':');
      const startInMinutes = start[0] * 60 + start[1] * 1;
      const endInMinutes = end[0] * 60 + end[1] * 1;
      const intervalInMinutes = 60;
      const times = [];
      for (let i = startInMinutes; i <= endInMinutes; i += intervalInMinutes) {
        let hour = Math.floor(i / 60) + '';
        let minute = (i % 60) + '';
        minute = minute.length < 2 ? '0' + minute : minute;
        hour = hour.length < 2 ? '0' + hour : hour;
        times.push(hour);
      }
      for (let i = 0; i < times.length - 1; i++) {
        this.slots.push(times[i] + ' - ' + times[i + 1]);
      }
    });
    this.displaySlot();
  }

  showData() {
    this.hideData = false;
  }
  /**
   * @description This method is used to filter the data based on selected Date and Time
   */
  displaySlot() {
    this.slots.filter(obj => {
      if (obj > this.fullTime) {
        this.filteredData.push(obj);
      }
    });
    if (this.filteredData.length) {
      this.filteredDataShow = true;
      this.filteredDataHide = false;
    } else {
      this.filteredDataShow = false;
      this.filteredDataHide = true;
    }
    this.selectedSlot = this.filteredData[0];
    if (this.selectedSlot) {
      this.selectedSlotHours = this.selectedSlot.toString();
      this.selectedSlotHours = this.selectedSlotHours.substring(0, 2);
    }
  }

  /**
   * @description This method is used to selected slot for the patient.
   */
  selectedSessionHours(tabChangeEvent) {
    const i = tabChangeEvent.index;
    this.selectedSlot = this.filteredData.filter(val => {
      return this.filteredData.indexOf(val) === i;
    });
    if (this.selectedSlot) {
      this.selectedSlotHours = this.selectedSlot.toString();
      this.selectedSlotHours = this.selectedSlotHours.substring(0, 2);
    }
  }
  /**
   * @description This method is used to display the doctorAppointmentSlots based on the selected time.
   */
  doctorsAvailableSlots() {
    const payload = {
      empId: this.empId,
      orgGrpId: this.userSession.orgGroupId,
      orgId: this.userSession.orgId,
      regId: this.userSession.regId,
      searchDt: this.visitDate,
      searchDay: this.visitDay
    };
    this.filteredData = [];
    this.slots = [];
    this.appointmentService.fetchDoctorSettingsInfo(payload).subscribe(
      response => {
        if (response.responseCode === 'E200') {
          this.doctorData = response.data;
          if (
            this.doctorData &&
            this.doctorData.organisationInfoList &&
            this.doctorData.organisationInfoList.length
          ) {
            this.orgInfo = this.doctorData.organisationInfoList;
            this.orgInfo.forEach(element => {
              if (element.docAppointmentSlots) {
              }
              const data1 = element.docAppointmentSlots;
              if (this.orgInfo && data1 && data1.length) {
                this.doctorAppSlot = data1;
                this.doctorAppSlot.forEach(element1 => {
                  this.doctorSlotTimings = element1.slotTimings;
                  this.sessions = [];
                  this.doctorSlotTimings.forEach(element2 => {
                    this.sessions.push(element2);
                  });
                });
                this.slotDuration = element.slotDurationFeeInfo.apptDuration
                  ? element.slotDurationFeeInfo.apptDuration
                  : 0;
              }
            });
          }
          this.slotsData();
        } else if (response.responseCode === 'E400') {
          this.toastr.error('Failure!', response.responseMessage);
        }
      },
      () => {
        this.toastr.error('Failure!', 'Something went wrong');
      }
    );
  }
  cancelAppointment() {
    this.sideNavRef.close();
    this.cancelNav.open();
    this.individualPtCancel = true;
    this.cancelSideNav = false;
    this.cancelledSideNav = false;
    const cancelledNavData = {
      cancelledSideNav: this.cancelledSideNav,
      cancelSideNav: this.cancelSideNav,
      individualPtCancel: this.individualPtCancel
    };
    this.doctorService.cancelledNavData.next(cancelledNavData);
    this.doctorService.bookAppointmentSlot.next(this.selectedPatientDetails);
    console.log(this.selectedPatientDetails);
  }
  ngOnDestroy() {}
}
