import { Component, OnInit, Input, ViewChild } from '@angular/core';
import { DoctorService } from '../../services/doctor.service';
import { MatSidenav } from '@angular/material';


@Component({
  selector: 'app-completed-appointment',
  templateUrl: './completed-appointment.component.html',
  styleUrls: ['./completed-appointment.component.scss']
})
export class CompletedAppointmentComponent implements OnInit {
  @Input() sideNavRef: MatSidenav;
  d = new Date();
  public serviceName = [];
  public completedAppointments;
  public bookSideNav: any;


  constructor(private doctorService: DoctorService) {}

  closeSideNav() {
    this.sideNavRef.close();
  }

  ngOnInit() {
this.doctorService.appointmentSlot.subscribe((data) => {
      this.completedAppointments = data;
    });
  this.doctorService.bookEditApp.subscribe((data) => {
      this.bookSideNav = data;
    });
  }
  reBook() {
    this.sideNavRef.close();
    this.doctorService.completeAppointmentData.next(this.completedAppointments);
    this.bookSideNav.open();
  }
}
