import { NgModule } from '@angular/core';
import { Routes, RouterModule, Router } from '@angular/router';

import { DoctorslistComponent } from './containers/doctorslist/doctorslist.component';
import { DoctornewscheduleComponent } from './containers/doctornewschedule/doctornewschedule.component';
import { BookanappointmentsidenavComponent } from './containers/bookanappointmentsidenav/bookanappointmentsidenav.component';
// tslint:disable-next-line:max-line-length
// tslint:disable-next-line:max-line-length
const appoinment: Routes = [
  {
    path: '',
    component: DoctorslistComponent,
    children: [
      { path: 'doctorslist', component: DoctorslistComponent },
      { path: 'doctornewschedule', component: DoctornewscheduleComponent },
      { path: 'bookanappointmentsidenav', component: BookanappointmentsidenavComponent }
    ]
  }
  // { path: '', redirectTo: 'doctorslist', pathMatch: 'prefix' }
];

@NgModule({
  imports: [RouterModule.forChild(appoinment)],
  exports: [RouterModule]
})
export class AppointmentRoutingMoule {}
