import {
  Component,
  OnInit,
  ViewEncapsulation,
  AfterViewInit,
  ViewChild,
  ChangeDetectionStrategy
} from '@angular/core';
import { MatDatepicker } from '@angular/material/datepicker';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { AppointmentApiService } from '@core/services/appointment-api.service';
import { UserService } from '@core/services/user.service';
import { UserSession } from '@core/models/user-session';
import { ToastrService } from 'ngx-toastr';
import { DoctorService } from '../../services/doctor.service';
import { Subscription } from 'rxjs';
import { NgbDatepicker } from '@ng-bootstrap/ng-bootstrap';
import { NgbCalendar, NgbDateStruct } from '@ng-bootstrap/ng-bootstrap';
import { ConfirmationDialogComponent } from '@shared/components/confirmation-dialog/confirmation-dialog.component';
import { MatDialog } from '@angular/material';
import {slideDownAnimation} from '@core/animations';

@ Component({
  selector: 'app-doctornewschedule',
  templateUrl: './doctornewschedule.component.html',
  styleUrls: ['./doctornewschedule.component.scss'],
  encapsulation: ViewEncapsulation.None,
  animations: [slideDownAnimation],
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class DoctornewscheduleComponent implements OnInit, AfterViewInit {

  @ViewChild('dp')
  dp: NgbDatepicker;

  cost: any;
  currency: any;
  doctorsSettingsSchedule: any;
  defaultTimings = [
    '00:00',
    '00:30',
    '01:00',
    '01:30',
    '02:00',
    '02:30',
    '03:00',
    '03:30',
    '04:00',
    '04:30',
    '05:00',
    '05:30',
    '06:00',
    '06:30',
    '07:00',
    '07:30',
    '08:00',
    '08:30',
    '09:00',
    '09:30',
    '10:00',
    '10:30',
    '11:00',
    '11:30',
    '12:00',
    '12:30',
    '13:00',
    '13:30',
    '14:00',
    '14:30',
    '15:00',
    '15:30',
    '16:00',
    '16:30',
    '17:00',
    '17:30',
    '18:00',
    '18:30',
    '19:00',
    '19:30',
    '20:00',
    '20:30',
    '21:00',
    '21:30',
    '22:00',
    '22:30',
    '23:00',
    '23:30',
    '24:00'
  ];
  duration: any;
  editIndex: number;
  formGroup: FormGroup;
  isOpen = false;
  isEdit = false;
  minDate = new Date();
  orgData: any;
  submitted = false;
  slotDuration: any;
  view: any;
  public addNewScdulePopover: number;
  public endDt: any;
  public model: NgbDateStruct;
  public orgArray = [];
  public scheduleArray = [];
  public slotTimings: any;
  public startDt: any;
  private addingSlots = [];
  private calanderDataSubscription: Subscription;
  private doctorResult: any;
  private doctorDataSubjectSubscription: Subscription;
  private doctorDataSubscription: Subscription;
  private docAppointmentSlots: any;
  private monthDataSubscription: Subscription;
  private orgInfo: any;
  private selectedDoctorData: any;
  private sessionData: UserSession;
  private slotTime = [];
  private slotDurationFeeInfo: any;
  private viewDataSubscription: Subscription;
  private weekCalendarDataSubscription: Subscription;
  private weekDataSubscription: Subscription;

  constructor(
    private addNewScheduleBuilder: FormBuilder,
    private appointmentService: AppointmentApiService,
    private userService: UserService,
    private toaster: ToastrService,
    private doctorservice: DoctorService,
    private calander: NgbCalendar,
    private dialog: MatDialog
  ) { }

  ngOnInit() {
    this.sessionData = this.userService.getUser();
    this.isEdit = false;

    // this subscription for get the selected practiioner details
    this.doctorDataSubjectSubscription = this.doctorservice.doctorDataSubject.subscribe(
      data => {
        if (data) {
          this.selectedDoctorData = data;
          this.orgData = this.selectedDoctorData.organisationInfoList[0];
          this.getSelectedDoctorData();
        }
      });

    // This subscription for get doctor data for construnctin payload for save schedule
    this.doctorDataSubscription = this.doctorservice.doctorData.subscribe(
      data => {
        this.doctorResult = data;
      });

    this.initForm();
  }

  ngAfterViewInit() {
    // this is view changed subscription
    this.viewDataSubscription = this.doctorservice.viewData.subscribe(val => {
      this.view = val;
      this.model = this.calander.getToday();
      this.dp.navigateTo();
      if (this.view === 'week') {
        // this subscription to get the date when changed the date from week header calander
        this.weekCalendarDataSubscription = this.doctorservice.WeekCalendarData.subscribe(
          res => {
            if (res) {
              const year = Number(res.slice(0, 4));
              const month = Number(res.slice(5, 7));
              const day = Number(res.slice(8, 11));
              this.model = { year: year, month: month, day: day };
              this.onDateChange(this.model);
            }
          });
      }
    });

    // this subscription to get the date when date change in day calander
    this.calanderDataSubscription = this.doctorservice.calendarData.subscribe(
      val => {
        if (val) {
          this.navigateCalander(val);
          if (this.view === 'day') {
            this.doctorservice.clickedDate.next(val);
          }
        }
      });

    // This subscription for get the date when date chenged in week calander
    this.weekDataSubscription = this.doctorservice.weekData.subscribe(val => {
      if (val) {
        this.navigateCalander(val);
      }
    });

    // This subscription for get the changed month data from month calander
    this.monthDataSubscription = this.doctorservice.monthData.subscribe(val => {
      if (val) {
       this.navigateCalander(val);
      }
    });
  }

  /**
   * @description This method is used to open and close the doctor schedule form.
   */
  addNewSchedule(i) {
    this.formGroup.reset();
    this.formGroup.patchValue({ appointmentType: 'walkin' });
    this.formGroup.patchValue({
      weekDays: {
        sunday: false,
        monday: false,
        tuesday: false,
        wednesday: false,
        thursday: false,
        friday: false,
        saturday: false
      }
    });
    this.formGroup.patchValue({ duration: '1' });
    this.formGroup.patchValue({ currency: 'INR' });
    this.addingSlots = [];
    this.submitted = false;
    this.isEdit = false;
    if (this.addNewScdulePopover !== i) {
      this.addNewScdulePopover = i;
    } else {
      this.addNewScdulePopover = undefined;

    }
  }

  /**
   * @description This method is used to add the slot timingd to payload inlcuding time and dates.
   */
  addTime() {
    const fromTm = this.formGroup.get('startTime').value;
    this.slotDuration = {
      fromTm: this.formGroup.get('startTime').value,
      toTm: this.formGroup.get('endTime').value,
      weekdaysgroup: this.formGroup.get('weekDays').value,
      startDt: this.formGroup.get('fromDt').value,
      endDt: this.formGroup.get('toDt').value
    };
    this.addingSlots.push(this.slotDuration);
    this.formGroup.get('startTime').reset(),
      this.formGroup.get('endTime').reset();
  }

  /**
   * @description This method is used to close and reset the doctor schedule form.
   */
  closeSChedule() {
    this.isEdit = false;
    this.submitted = false;
    this.isOpen = false;

    this.formGroup.reset();
    this.formGroup.patchValue({ appointmentType: 'walkin' });
    this.formGroup.patchValue({
      weekDays: {
        sunday: false,
        monday: false,
        tuesday: false,
        wednesday: false,
        thursday: false,
        friday: false,
        saturday: false
      }
    });
    this.addNewScdulePopover = undefined;
    this.formGroup.patchValue({ duration: '1' });
    this.formGroup.patchValue({ currency: 'INR' });
    this.addingSlots = [];
  }

  /**
   * @description this method is used for open, close the mat date picker.
   */
  datePickerToggler(datePicker: MatDatepicker<Date>) {
    datePicker[datePicker.opened ? 'close' : 'open']();
  }

  /**
   * @description this method is used for delete the doctor schedule from DB.
   * @function deleteDoctorSchedule
   */
  deleteSlotTimings(index) {
    const dialogRef = this.dialog.open(ConfirmationDialogComponent, {
      data: { title: 'Are You Sure Want To Delete ?' },
      disableClose: true
    });
    dialogRef.afterClosed().subscribe(result => {
      if (result) {
        const payload = {
          orgId: this.sessionData.orgId,
          empId: this.orgInfo.docId,
          startDt: this.orgArray[index - 1].startDt,
          endDt: this.orgArray[index - 1].endDt
        };
        this.appointmentService.deleteDoctorSchedule(payload).subscribe(
          response => {
            if (response && response.responseCode === 'E200') {

              if (response.responseMessage === 'DELETED SUCESSFULLY') {
                this.scheduleArray.splice(index - 1, 1);
                this.doctorservice.isDocAppointmentAvilable.next(true);
                this.toaster.success('success', response.responseMessage);
              }
              if (
                response.responseMessage ===
                'Appointments Are Mapped with These Slots. Please Delete Those Appointments'
              ) {
                this.toaster.warning('warning', response.responseMessage);
              }
            } else if (response && response.responseCode === 'E400') {
              this.toaster.error('Failure!', response.responseMessage);
            }
          },
          () => {
            this.toaster.error('Something Went Wrong');
          }
        );
      }
    });
  }

  /**
   * @description This method is used to edit the doctor schedule.
   */
  editSlotsTimings(obj) {
    this.editIndex = obj;
    const editSchedule = this.orgArray[this.editIndex - 1];
    this.isEdit = true;
    this.isOpen = true;
    this.addNewScdulePopover = this.editIndex;
    this.formGroup.patchValue({ fromDt: editSchedule.startDt });
    this.formGroup.patchValue({ toDt: editSchedule.endDt });
    this.formGroup.patchValue({
      weekDays: {
        sunday: editSchedule.sunday,
        monday: editSchedule.monday,
        tuesday: editSchedule.tuesday,
        wednesday: editSchedule.wednesday,
        thursday: editSchedule.thursday,
        friday: editSchedule.friday,
        saturday: editSchedule.saturday
      }
    });
    this.addingSlots = [];
    editSchedule.slotTimings.forEach(element => {
      const slotTime = [element];
      this.slotDuration = {
        weekdaysgroup: editSchedule.apptSlotDays,
        fromTm: element.fromTm,
        toTm: element.toTm,
        startDt: this.startDt,
        endDt: this.endDt,
        slotTime: slotTime
      };
      this.addingSlots.push(this.slotDuration);
    });
    this.formGroup.patchValue({ duration: this.duration });
    this.formGroup.patchValue({ currency: this.currency });
    this.formGroup.patchValue({ cost: this.cost });
  }

  /**
   * @description this method is used for get the form controls to validate the form when saving doctors schedule
   */
  get form() {
    return this.formGroup.controls;
  }

  /**
   * @description this method is used for get doctors data from service file.
   * @function doctorDataSubject
   */
  getSelectedDoctorData() {
    if (this.selectedDoctorData) {
      this.orgInfo = this.selectedDoctorData.organisationInfoList[0];
      this.orgArray = this.orgInfo.docAppointmentSlots;
      this.scheduleArray = [];
      this.orgArray.forEach(val => {
        this.scheduleArray.push(val);
      });
      this.scheduleArray.unshift({name: 'Add New Schedule'});
      if (this.orgInfo) {
        this.slotDurationFeeInfo = this.orgInfo.slotDurationFeeInfo;
        this.docAppointmentSlots = this.orgInfo.docAppointmentSlots[0];
        if (this.docAppointmentSlots) {
          this.startDt = this.docAppointmentSlots.startDt;
          this.endDt = this.docAppointmentSlots.endDt;
          this.slotTimings = this.docAppointmentSlots.slotTimings;
          if (this.slotTimings) {
            this.slotTime = [];
            this.slotTimings.forEach(element => {
              const obj = {
                fromTm: element.fromTm,
                toTm: element.toTm
              };
              this.slotTime.push(obj);
            });
          }
          this.doctorservice.isDocAppointmentAvilable.next(true);
        } else {
          this.doctorservice.isDocAppointmentAvilable.next(false);
        }
        if (this.slotDurationFeeInfo) {
          this.duration = this.slotDurationFeeInfo.apptDuration;
          this.currency = this.slotDurationFeeInfo.currencyCode;
          this.cost = this.slotDurationFeeInfo.inPersonApptFee;
        }
      }
    }
  }

  /**
   * @description This method is used to declare list of form control names with validation
   */
  initForm() {
    this .formGroup = this .addNewScheduleBuilder.group({
      appointmentType: ['walkin', [Validators.required]],
      fromDt: [null, [Validators.required]],
      toDt: [null, [Validators.required]],
      weekDays: this .addNewScheduleBuilder.group({
        sunday: false,
        monday: false,
        tuesday: false,
        wednesday: false,
        thursday: false,
        friday: false,
        saturday: false
      }),
      startTime: [null],
      endTime: [null],
      duration: [1, [Validators.required, Validators.maxLength(2)]],
      currency: ['INR', [Validators.required]],
      cost: [null, [Validators.required, Validators.maxLength(8)]]
    });

    this.formGroup.get('fromDt').valueChanges.subscribe(val => {
      if (val && this.isEdit) {
        this.startDt = val;
      }
      this.formGroup.get('toDt').reset();
    });

    this.formGroup.get('toDt').valueChanges.subscribe(val => {
      if (val && this.isEdit) {
        this.addingSlots = [];
        const editScedhuleData = this.orgArray[this.editIndex - 1];
        editScedhuleData.slotTimings.forEach(element => {
          const obj = {
            fromTm: element.fromTm,
            toTm: element.toTm,
            startDt: this.startDt,
            endDt: val
          };
          this.addingSlots.push(obj);
        });
      }
    });

    this.formGroup.get('startTime').valueChanges.subscribe(val => {
      this.formGroup.get('endTime').reset();
      if (val) {
        const startTime = this .formGroup.get('startTime').value;
        this .selectedSessionTimings(startTime);
      }
    });

    this.formGroup.get('endTime').valueChanges.subscribe(val => {
      if (val) {
        const diffTime = val < this.formGroup.get('startTime').value;
        if (diffTime) {
          this.formGroup.get('endTime').reset();
        }
        this.selectedSessionTimings(val);
      }
    });

    this.formGroup.get('duration').valueChanges.subscribe(val => {
      const isDuration = this.formGroup.get('duration').value > 60;
      if (isDuration) {
        this.formGroup.get('duration').reset();
      }
    });
  }

navigateCalander(val) {
  const year = val.getFullYear();
  const month = val.getMonth() + 1;
  const day = val.getDate();
  this.model = { year: year, month: month, day: day };
  this.dp.navigateTo({ year: year, month: month, day: day });
}

  /**
   * @description This method is used to get the date from calander when date changed
   * @param model
   */
  onDateChange(model) {
    const year = model.year;
    const month = model.month;
    let day = model.day.toString();
    if (day.length < 2) {
      day = '0' + day;
    }
    const totalDate = year + '-' + month + '-' + day;
    this.doctorservice.clickedDate.next(totalDate);
  }
  isDisabled(index) {
      if (index === 0) {
        return false;
      } else {
        return true;
      }
  }
  /**
   * @description This method is used to get the changed month
   * @param event
   */
  onMonthChange(event) {
    if (this.view === 'month') {
      const today = new Date().getDate();
      const clickeData = event.next;
      const date = clickeData.year + '-' + clickeData.month + '-' + today;
      this.doctorservice.monthDataFromDocSchedule.next(date);
    }
  }

  /**
   * @description this method is used for delete the session from payload
   */
  removeSlot(index) {
    this.addingSlots.splice(index, 1);
  }

  /**
   * @description this method is used for validate the startTime and endTime.
   */
  selectedSessionTimings(time) {
    const startTime = this.formGroup.get('startTime').value;
    const isValidTime =
      this.defaultTimings.indexOf(time) -
      this.defaultTimings.indexOf(startTime) > 24;
    if (isValidTime) {
      this.formGroup.get('startTime').reset();
      this.formGroup.get('endTime').reset();
    }
    if (this.addingSlots.length) {
      this.addingSlots.forEach(ele => {
        const newArr = this.defaultTimings.filter(val => {
          return val <= ele.toTm;
        });
        const isAvlTime = newArr.includes(time);
        if (isAvlTime) {
          this .formGroup.get('startTime').reset();
          this .formGroup.get('endTime').reset();
        }
      });
    }
  }

  /**
   * @description this method is used for post the doctor scheduled data to API.
   * @function saveDoctorSchedule
   *
   * @description this method is used to get the doctor scheduled data from API.
   * @function fetchDoctorSettingsInfo
   */
  saveSchedule() {
    this.orgData = this.selectedDoctorData.organisationInfoList[0];
    this.submitted = true;
    if (!this.addingSlots.length || this.formGroup.invalid) {
      return;
    }
    const weekdayData = this.formGroup.get('weekDays').value;
    const obj = {
      sunday: weekdayData.sunday,
      monday: weekdayData.monday,
      tuesday: weekdayData.tuesday,
      wednesday: weekdayData.wednesday,
      thursday: weekdayData.thursday,
      friday: weekdayData.friday,
      saturday: weekdayData.saturday,
      slotTimings: this.addingSlots,
      isWeekNotSelected: false
    };
    if (this.isEdit) {
      this.orgArray[this.editIndex - 1] = obj;
    } else {
      this.orgArray.push(obj);
    }
    this.orgArray.forEach(element => {
      delete element.apptSlotDays,
      delete element.endDt,
      delete element.startDt;
    });
    const payload = {
      empId: this.orgData.docId,
      regId: this.sessionData.regId,
      orgId: this.sessionData.orgId,
      orgGrpId: this.sessionData.orgGroupId,
      organisationInfoList: [
        {
          orgId: this.sessionData.orgId,
          orgName: this.sessionData.orgList[0].orgName,
          bedsType: this.sessionData.bedsType,
          registerNumber: this.orgData.registerNo,
          otOrgType: this.orgData.otOrgType,
          docId: this.orgData.docId,
          orgGrpId: this.sessionData.orgGroupId,
          slotDurationFeeInfo: {
            docId: this.orgData.docId,
            orgId: this.sessionData.orgId,
            orgGrpId: this.sessionData['orgGroupId'],
            apptDuration: this.formGroup.get('duration').value,
            inPersonApptFee: this.formGroup.get('cost').value,
            textApptFee: this.formGroup.get('cost').value,
            audioApptFee: this.formGroup.get('cost').value,
            videoApptFee: null,
            currencyCode: this.formGroup.get('currency').value
          },
          docAppointmentSlots: this.orgArray,
          country: this.orgData.country,
          mandal: this.orgData.mandal,
          village: this.orgData.village,
          locality: this.orgData.village,
          addressLine1: this.orgData.addressLine1,
          regVerified: this.orgData.regVerified,
          city: this.orgData.city,
          pinCode: this.orgData.pinCode,
          isHeadOffice: true,
          isOpen: true
        }
      ],
      yrsOfExpValue: this.doctorResult.yrsOfExp,
      isShareable: false,
      specalityInfoList: [],
      qualificationList: [],
      employeeLangInfoList: []
    };
    const payload1 = {
      empId: this.orgData.docId,
      orgId: this.sessionData.orgId,
      regId: this.sessionData.regId
    };
    this.appointmentService.saveDoctorSchedule<any>(payload).subscribe(
      response => {
        if (response.responseCode === 'E200') {
          this.doctorsSettingsSchedule = response.data;
          this.appointmentService
            .fetchDoctorSettingsInfo<any>(payload1)
            .subscribe(
              response1 => {
                if (response1.responseCode === 'E200') {
                  this.selectedDoctorData = response1.data;
                  this.getSelectedDoctorData();
                } else if (response1.responseCode === 'E400') {
                  this.toaster.error('Failure!', 'Something went wrong');
                }
              },
              _ => {
                this.toaster.error('Failure!', 'Something went wrong');
              }
            );
        } else if (response.responseCode === 'E400') {
          this .toaster.error('Failure!', 'Something went wrong');
        }
      },
      _ => {
        this .toaster.error('Failure!', 'Something went wrong');
      }
    );
    this.formGroup.reset();
    this.formGroup.patchValue({ appointmentType: 'walkin' });
    this.formGroup.patchValue({
      weekDays: {
        sunday: false,
        monday: false,
        tuesday: false,
        wednesday: false,
        thursday: false,
        friday: false,
        saturday: false
      }
    });
    this.formGroup.patchValue({ duration: '1' });
    this.formGroup.patchValue({ currency: 'INR' });
    this.addingSlots = [];
    this.addNewScdulePopover = undefined;
    this.submitted = false;
    this.isEdit = false;
  }
}
