import { Component, OnInit, Input, AfterViewInit } from '@angular/core';
import { MatSidenav, MatDatepicker } from '@angular/material';
import { FormGroup, Validators, FormBuilder } from '@angular/forms';
import { DoctorService } from '../../services/doctor.service';
import { AppointmentApiService } from '@core/services/appointment-api.service';
import { ToastrService } from 'ngx-toastr';
import { UserService } from '@core/services';
import { UserSession } from '@core/models/user-session';
import { DatePipe } from '@angular/common';

@Component({
  selector: 'app-view-and-edit-appointment',
  templateUrl: './view-and-edit-appointment.component.html',
  styleUrls: ['./view-and-edit-appointment.component.scss']
})
export class ViewAndEditAppointmentComponent implements OnInit, AfterViewInit {
  @Input() sideNavRef: MatSidenav;
  bookViewForm: FormGroup;
  public cancelledSideNav = false;
  public cancelSideNav = false;
  d = new Date();
  public doctorData;
  public doctorSlotTimings;
  public doctorAppSlot;
  empId: any;
  fromDay: any;
  public filterDataShow = false;
  public filterDataHide = true;
  public services;
  // public hosPatientId;
  // public name;
  // public mobileNo;

  public isShowTime = false;
  public viewAndEditAppointment;
  public slot = true;
  public individualPtCancel = false;

  public sessions = [];
  public orgInfo;

  public slotDuration;
  public selectedSlotHours;
  public slots = [];
  public slotData: any;

  public index = [];
  public visit;
  public minDate = new Date();
  public cancelNav: any;
  fullTime: string;
  public week = [
    'Sunday',
    'Monday',
    'Tuesday',
    'Wednesday',
    'Thursday',
    'Friday',
    'Saturday'
  ];
  filteredData = [];
  selectedSlot: any[];
  visitDate: string;
  selectedDate: any;
  visitDay: string;
  pastDate: any;
  doctorName: string;

  constructor(
    private doctorService: DoctorService,
    private userService: UserService,
    private appointmentService: AppointmentApiService,
    private toastr: ToastrService,
    private datePipe: DatePipe,
    private bookFormBuilder: FormBuilder
  ) {
    this.userSession = this.userService.getUser();
  }
  private userSession: UserSession;

  closeSideNav() {
    this.sideNavRef.close();
    this.selectedSlotHours = '';
    this.filteredData = [];
    this.filterDataShow = false;
    this.filterDataHide = false;
  }

  ngOnInit() {
    this.initForm();
    this.doctorService.doctorData.subscribe( (data) => {
      if (data) {
this.doctorName = data.doctorName;
      }

    });
    this.doctorService.sideNavData.subscribe(data => {
      this.cancelNav = data.cancelNav;
    });
    this.doctorService.doctorDataSubject.subscribe(data => {
      this.doctorData = data;
      if (
        this.doctorData &&
        this.doctorData.organisationInfoList &&
        this.doctorData.organisationInfoList.length
      ) {
        this.orgInfo = this.doctorData.organisationInfoList;
        this.orgInfo.forEach(element => {
          const data1 = element.docAppointmentSlots;
          if (this.orgInfo && data1 && data1.length) {
            this.doctorAppSlot = data1;
            this.doctorAppSlot.forEach(element1 => {
              this.doctorSlotTimings = element1.slotTimings;
              this.sessions = [];
              this.doctorSlotTimings.forEach(element2 => {
                this.sessions.push(element2);
              });
            });
            this.slotDuration = element.slotDurationFeeInfo.apptDuration
              ? element.slotDurationFeeInfo.apptDuration
              : 0;
            this.slotsTimingsData();
          }
        });
      }
    });
    this.doctorService.appointmentSlot.subscribe(data => {
      if (data) {
        this.viewAndEditAppointment = data;
        this.pastDateAndSlot();
      }


      // const previousDate = this.datePipe.transform (this.viewAndEditAppointment.date, 'yyyy-MM-dd');
      // const previousTime = this.viewAndEditAppointment.time;
      // this.pastDate = previousDate + '' + previousTime + ':00';
      // console.log(this.pastDate);

      this.filterDataHide = false;
      this.filterDataShow = false;
    });
  }
  initForm() {
    this.bookViewForm = this.bookFormBuilder.group({
      service: '',
      patientSearchData: '',
      fromDt: ['', Validators.required]
    });
    this.bookViewForm.get('fromDt').valueChanges.subscribe(data => {
      this.sessions = [];
      const fromDt = this.bookViewForm.get('fromDt').value;
      if (fromDt) {
        this.fromDay = fromDt.getDay();
      }
      this.visit = this.datePipe.transform(fromDt, 'yyyy-MM-dd:HH:mm');
      const date = this.visit;
      this.visitDate = this.datePipe.transform(fromDt, 'yyyy-MM-dd');
      this.visitDay = this.week[this.fromDay];
      if (date) {
        this.doctorsAvailableSlots();
        this.selectedDate = date.substring(0, 10);
        const time = this.visit;
        const startTime = time.substring(11, 13);
        const end = 1;
        const endTime = +startTime + end;
        this.fullTime = startTime + ' - ' + endTime;
        this.selectedSlotHours = time.substring(11, 13);
      }
    });
  }
  ngAfterViewInit() {
    this.doctorService.doctorDataSubject.subscribe(data => {
      this.doctorData = data;

      if (
        this.doctorData &&
        this.doctorData.organisationInfoList &&
        this.doctorData.organisationInfoList.length
      ) {
        this.orgInfo = this.doctorData.organisationInfoList;
        this.empId = this.orgInfo[0].docId;
      }
    });
  }
  /**
   * @description This method is used to calculate the slots based on the doctorSelected.
   */
  slotsTimingsData() {
    this.slots = [];
    this.sessions.forEach(element => {
      const start = element.fromTm.split(':');
      const end = element.toTm.split(':');
      const startInMinutes = start[0] * 60 + start[1] * 1;
      const endInMinutes = end[0] * 60 + end[1] * 1;
      // tslint:disable-next-line:radix
      const intervalInMinutes = 60;
      const times = [];
      for (let i = startInMinutes; i <= endInMinutes; i += intervalInMinutes) {
        let hour = Math.floor(i / 60) + '';
        let minute = (i % 60) + '';
        minute = minute.length < 2 ? '0' + minute : minute;
        hour = hour.length < 2 ? '0' + hour : hour;
        times.push(hour);
      }
      for (let i = 0; i < times.length - 1; i++) {
        this.slots.push(times[i] + ' - ' + times[i + 1]);
      }
    });
    this.displaySlot();
  }
  /**
   * @description This method is used to filter the data based on selected Date and Time
   */
  displaySlot() {
    this.slotData = this.slots;
    this.slotData.filter(obj => {
      if (obj > this.fullTime) {
        this.filteredData.push(obj);
      }
    });
    if (this.filteredData.length) {
      this.filterDataShow = true;
      this.filterDataHide = false;
    } else {
      this.filterDataShow = false;
      this.filterDataHide = true;
    }
    this.selectedSlot = this.filteredData[0];
    if (this.selectedSlot) {
      this.selectedSlotHours = this.selectedSlot.toString();
      this.selectedSlotHours = this.selectedSlotHours.substring(0, 2);
    }
  }
  hide() {
    this.isShowTime = true;
    this.slot = false;
  }
  /**
   * @description This method is used to selected slot for the patient.
   */
  selectedSessionHours(tabChangeEvent) {
    const i = tabChangeEvent.index;
    this.selectedSlot = this.filteredData.filter(val => {
      return this.filteredData.indexOf(val) === i;
    });
    if (this.selectedSlot) {
      this.selectedSlotHours = this.selectedSlot.toString();
      this.selectedSlotHours = this.selectedSlotHours.substring(0, 2);
    }
  }
  /**
   * @description This method is used to bookanAppointment for the Patient.
   */
  bookAppointment() {
    const payload = {
      refDoctorNm: this.doctorName,
      isUpdate : true,
      doctorId: this.viewAndEditAppointment.doctorId,
      hosPatientId: this.viewAndEditAppointment.hosPatientId,
      orgId: this.userSession.orgId,
      orgGrpId: this.userSession.orgGroupId,
      patientId: this.viewAndEditAppointment.patientId,
      patientMailId: this.viewAndEditAppointment.email,
      patientMobile: this.viewAndEditAppointment.mobileNo,
      patientNm: this.viewAndEditAppointment.name,
      serviceId: this.viewAndEditAppointment.serviceId,
      serviceNm: this.viewAndEditAppointment.services,
      visitDttm:
        this.selectedDate + ' ' + this.selectedSlotHours + ':00' + ':00',
        pastAppmtDt : this.pastDate
    };
    this.appointmentService.bookAppointment<any>(payload).subscribe(
      response => {
        if (response.responseCode === 'E200') {
          this.toastr.success('Success!', response.responseMessage);
          this.selectedSlotHours = '';
          this.isShowTime = false;
          this.slot = true;
          this.doctorService.isDocAppointmentAvilable.next(true);
        } else if (response.responseCode === 'E400') {
          this.toastr.error('Failure!', response.responseMessage);
          this.doctorService.isDocAppointmentAvilable.next(false);
        }
      },
      () => {
        this.toastr.error('Failure!', 'Something went wrong');
      }
    );
  }

  cancelAppointment() {
    this.sideNavRef.close();
    this.cancelNav.open();
    this.individualPtCancel = true;
    this.cancelSideNav = false;
    this.cancelledSideNav = false;
    const cancelledNavData = {
      cancelledSideNav: this.cancelledSideNav,
      cancelSideNav: this.cancelSideNav,
      individualPtCancel: this.individualPtCancel
    };
    this.doctorService.cancelledNavData.next(cancelledNavData);
    this.doctorService.viewAppointmentSlot.next(this.viewAndEditAppointment);
  }
  /**
   * @description This method is used to display the doctorAppointmentSlots based on the selected time.
   */
  doctorsAvailableSlots() {
    const payload = {
      empId: this.empId,
      orgGrpId: this.userSession.orgGroupId,
      orgId: this.userSession.orgId,
      regId: this.userSession.regId,
      searchDt: this.visitDate,
      searchDay: this.visitDay
    };
    this.filteredData = [];
    this.slots = [];
    this.appointmentService.fetchDoctorSettingsInfo(payload).subscribe(
      response => {
        if (response.responseCode === 'E200') {
          this.doctorData = response.data;
          if (
            this.doctorData &&
            this.doctorData.organisationInfoList &&
            this.doctorData.organisationInfoList.length
          ) {
            this.orgInfo = this.doctorData.organisationInfoList;
            this.orgInfo.forEach(element => {
              if (element.docAppointmentSlots) {
              }
              const data1 = element.docAppointmentSlots;
              if (this.orgInfo && data1 && data1.length) {
                this.doctorAppSlot = data1;
                this.doctorAppSlot.forEach(element1 => {
                  this.doctorSlotTimings = element1.slotTimings;
                  this.sessions = [];
                  this.doctorSlotTimings.forEach(element2 => {
                    this.sessions.push(element2);
                  });
                });
                this.slotDuration = element.slotDurationFeeInfo.apptDuration
                  ? element.slotDurationFeeInfo.apptDuration
                  : 0;
              }
            });
          }
          this.slotsTimingsData();
        } else if (response.responseCode === 'E400') {
          this.toastr.error('Failure!', response.responseMessage);
        }
      },
      () => {
        this.toastr.error('Failure!', 'Something went wrong');
      }
    );
  }
  pastDateAndSlot() {
    const previousData = this.datePipe.transform (this.viewAndEditAppointment.date, 'yyyy-MM-dd');
    const previousTime =  this.viewAndEditAppointment.time;
    this.pastDate = previousData + ' '  + previousTime + ':00' ;
  }
}
