import { Pipe, PipeTransform } from '@angular/core';
import { DatePipe } from '@angular/common';

@Pipe({
    name: 'searchFilter'
})

export class SearchFilterPipe implements PipeTransform {
    private;
    transform(
        history: any,
        searchDate: string,
        patientId: string,
        patientNm: string,
        mobileNo: string,
        serviceNm: string,
        patientStatus: string
    ) {
        if (history && history.length) {
            return history.filter(patient => {
                if (patient.visitDt) {
                    if (searchDate && patient.visitDt.toLowerCase().indexOf(searchDate.toLowerCase()) === -1) {
                        return false;
                    }
                    if (searchDate && (searchDate.length === 10 || searchDate.length === 3 || searchDate.length === 2)) {
                        const spilteDate = searchDate.split('/') || [];
                        if (spilteDate) {
                            const reverseArr = spilteDate.reverse();
                            if (reverseArr && patient.visitDt.indexOf(reverseArr.join('/')) === -1) {
                                return false;
                            }
                        }
                    }
                }

                if (patient.hosPatientId) {
                    if (patientId &&
                        patient.hosPatientId.toLowerCase().indexOf(patientId.toLowerCase()) === -1 ) {
                        return false;
                    }
                }

                if (patient.patientNm) {
                    if (patientNm &&
                        patient.patientNm.toLowerCase().indexOf(patientNm.toLowerCase()) === -1 ) {
                        return false;
                    }
                }

                if (patient.mobileNo) {
                    if (mobileNo &&
                        patient.mobileNo.toLowerCase().indexOf(mobileNo.toLowerCase()) === -1 ) {
                        return false;
                    }
                }

                if (patient.serviceNm) {
                    if (serviceNm &&
                        patient.serviceNm.toLowerCase().indexOf(serviceNm.toLowerCase()) === -1 ) {
                        return false;
                    }
                }

                if (patient.patientStatus) {
                    if (patientStatus &&
                        patient.patientStatus.toLowerCase().indexOf(patientStatus.toLowerCase()) === -1 ) {
                        return false;
                    }
                }

                return true;
            });
        } else {
            return history;
        }
    }
}
