import { Component, OnInit, Input } from '@angular/core';
import { DoctorService } from '../../services/doctor.service';
import { Subscription } from 'rxjs';
import { MatSidenav } from '@angular/material';
import { UserSession } from '@core/models/user-session';
import { AppointmentApiService, UserService } from '@core/services';
import { ToastrService } from 'ngx-toastr';
import { ConfirmationDialogComponent } from '@shared/components/confirmation-dialog/confirmation-dialog.component';
import { MatDialog } from '@angular/material';
import { DatePipe } from '@angular/common';

@Component({
  selector: 'app-individual-patient-cancel',
  templateUrl: './individual-patient-cancel.component.html',
  styleUrls: ['./individual-patient-cancel.component.scss']
})
export class IndividualPatientCancelComponent implements OnInit {
  @Input()
  sideNavRef: MatSidenav;
public slotData;
  public isCancelledSuccefully = false;
  private appointmentId: any;
  private appointmentSlotSubscription: Subscription;
  private cancelAppointmentSubscription: Subscription;
  private patientId: any;
  private userSession: UserSession;
  comments: any;

  constructor(
    private doctorService: DoctorService,
    private userService: UserService,
    private appointmentService: AppointmentApiService,
    private toastr: ToastrService,
    private dialog: MatDialog,
    private datePipe: DatePipe,
  ) {
    this.userSession = this.userService.getUser();
  }

  ngOnInit() {

    this.appointmentSlotSubscription = this.doctorService.viewAppointmentSlot.subscribe((data) => {
      if (data) {
        console.log('view', data);

        const viewData = {
          name : data.name,
          hospitalPatientId : data.hosPatientId,
          mobileNumber : data.mobileNo,
          servicesName : data.services,
          date: data.date,
          patientId : data.patientId,
          patientAppointmentId: data.appointmentId,
          appointmentTime: data.time
        };
        this.slotData = viewData;
      }
    });
    this.appointmentSlotSubscription = this.doctorService.bookAppointmentSlot.subscribe((data) => {
      if (data) {
        console.log(data);
        const bookData = {
          name : data.firstName + ' ' + data.lastName,
          hospitalPatientId : data.hospitalPatientId,
          mobileNumber : data.mobileNumber,
          servicesName : data.serviceNm,
          date: data.appointmentDttm,
          patientId : data.patientId,
          patientAppointmentId: data.patientAppointmentId,
          // appointmentTime: data.time
        };
        this.slotData = bookData;
      }

    });

  }

  // timeConvert(time) {
  //   // Check correct time format and split into components
  //   time = time.toString().match(/^([01]\d|2[0-3])(:)([0-5]\d)(:[0-5]\d)?$/) || [time];
  //   if (time.length > 1) { // If time format correct
  //     time = time.slice(1);  // Remove full string match value
  //     time[5] = +time[0] < 12 ? ' AM' : ' PM'; // Set AM/PM
  //     time[0] = +time[0] % 12 || 12; // Adjust hours
  //   }
  //   this.time = time.join(''); // return adjusted time or original string
  // }


  /**
   * @description This method is used to close the side bar
   */
  closeSideNav() {
    this.sideNavRef.close();
  }

  /**
   * @description This method is used to cancel the appointment
   * @function cancelAppointment
   */
  cancelAppointment() {
    const dialogRef = this.dialog.open(ConfirmationDialogComponent, {
      data: { title: 'Are You Sure Want To Cancel Appointment ?' },
      disableClose: true
    });
    dialogRef.afterClosed().subscribe(result => {
      if (result) {
    // console.log(this.comments);
    const payload = {
      orgId: this.userSession.orgId,
      patientId:  this.slotData.patientId,
      patientAppointmentId: this.slotData.patientAppointmentId,
      comments: this.comments
    };
    this.cancelAppointmentSubscription = this.appointmentService.cancelAppointment(payload)
      .subscribe(
        (response: any) => {
          if (response.responseCode === 'E200') {
            this.sideNavRef.close();
            this.toastr.success('Appointment Canceled Succefully');
            this.isCancelledSuccefully = true;
            this.doctorService.isCancelledSuccefully.next(this.isCancelledSuccefully);
            this.doctorService.isDocAppointmentAvilable.next(this.isCancelledSuccefully);
          } else if (response.response === 'E400') {
            this.isCancelledSuccefully = false;
            this.doctorService.isCancelledSuccefully.next(this.isCancelledSuccefully);
            this.doctorService.isDocAppointmentAvilable.next(this.isCancelledSuccefully);
          }
        });
      }
    });
  }


}
