import { Component, OnInit, OnDestroy } from '@angular/core';
import { Subject, Observable, Subscription } from 'rxjs';
import { UserService, UtilService } from '@core/services';
import { ToastrService } from 'ngx-toastr';
import { FormGroup, FormBuilder, FormControl, AbstractControl, Validators } from '@angular/forms';
import { UserSession } from '@core/models/user-session';
import { DoctorService } from '../../services/doctor.service';
import { AppointmentApiService } from '@core/services/appointment-api.service';
import { AutoUnsubscribe } from 'ngx-auto-unsubscribe';
import { ConfirmationDialogComponent } from '@shared/components/confirmation-dialog/confirmation-dialog.component';
import { MatDialog } from '@angular/material';
@AutoUnsubscribe()
@Component({
  selector: 'app-for-patient',
  templateUrl: './for-patient.component.html',
  styleUrls: ['./for_patient.component.scss']
})
export class ForPatientComponent implements OnInit, OnDestroy {

  cancelPatientData: any;
  patientForm: FormGroup;
  reasonComments: any;
  selectPatientSearch: AbstractControl;
  public cancelSideNav: any;
  public isSelectePatient = false;
  public isShowptDtl = false;
  public mobileNum = true;
  public patientData: any;
  public placeholderValue: any;
  public searchType: any;
  public submitted = false;
  public selectedPatient: any;
  private cancelAppointmentSubscription: Subscription;
  private searchCancelPatientSubscription: Subscription;
  private sideNavDataSubscription: Subscription;
  private userSession: UserSession;
  orgInfo: any;
  empId: any;
  doctorData: any;
  checked = this.utilService.generateUniqueId();

  constructor(
    private appointmentService: AppointmentApiService,
    private patientFormBuilder: FormBuilder,
    private toastr: ToastrService,
    private userService: UserService,
    private doctorService: DoctorService,
    private dialog: MatDialog,
    private utilService: UtilService,
  ) {
    this.userSession = this.userService.getUser();
  }

  ngOnInit() {
    this.tyepOfPatient();
    this.initForm();
    this.doctorService.doctorDataSubject.subscribe(data => {
      this.doctorData = data;

      if (
        this.doctorData &&
        this.doctorData.organisationInfoList &&
        this.doctorData.organisationInfoList.length
      ) {
        this.orgInfo = this.doctorData.organisationInfoList;
        this.empId = this.orgInfo[0].docId;
      }
    });

    this.sideNavDataSubscription = this.doctorService.sideNavData.subscribe((data) => {
      this.cancelSideNav = data.cancelNav;
    });
  }

  /**
  * @description this api is used to  cancel the appointment
  * @function cancelAppointment
  */
  cancelAppointment() {
    const dialogRef = this.dialog.open(ConfirmationDialogComponent, {
      data: { title: 'Are You Sure Want To Cancel Appointment ?' },
      disableClose: true
    });
    dialogRef.afterClosed().subscribe(result => {
      if (result) {
    // this.submitted = true;
    // if (this.patientForm.invalid) {
    //   return;
    // }
    this.submitted = true;
    if (this.patientForm.invalid) {
      return;
    }
    const payload = {
      orgId: this.userSession.orgId,
      patientId: this.selectedPatient.patientId,
      patientAppointmentId: this.selectedPatient.patientAppointmentId,
      comments: this.patientForm.get('comments').value
    };
    this.cancelAppointmentSubscription = this.appointmentService.cancelAppointment(payload)
      .subscribe(
        (response: any) => {
          if (response.responseCode === 'E200') {
            this.toastr.success(response.responseMessage);
            this.selectedPatient = null;
            this.patientForm.reset();
            this.isShowptDtl = false;
            this.doctorService.isCancelledSuccefully.next(true);
          } else if (response.response === 'E400') {
            this.toastr.error('Failure!', response.responseMessage);
            this.doctorService.isCancelledSuccefully.next(false);
          }
        },
        () => {
          this.toastr.error('Failure!', 'Something went wrong');
        }
      );
      }
    });
  }

  /**
  * @description this method is used to close the sidenav
  */
  closeSideNav() {
    this.isShowptDtl = false;
    this.selectedPatient = null;
    this.cancelPatientData = null;
    this.patientForm.reset();
    this.cancelSideNav.close();
    this.mobileNum = true;
  }

  /**
   * @description This method is used to declare list of form control names with validation in a forPatientForm.
   */
  initForm() {
    this.patientForm = this.patientFormBuilder.group({
      patientData: null,
      comments: ['', Validators.required]
    });
    this.patientForm.get('patientData').valueChanges.subscribe(val => {
      this.patientData = val;
    });
    this.patientForm.get('comments').valueChanges.subscribe(val => {
      this.reasonComments = val;
    });

  }

  /**
   * @description this method is used to get the selected patient in list of patients
   */
  onPatientChange(selectedPatient) {
    this.isShowptDtl = true;
    this.selectedPatient = selectedPatient;
    this.isSelectePatient = false;
    if (this.selectedPatient === undefined) {
      this.isShowptDtl = false;
    }

  }

  /**
   * @description this api is used to search patients for cancelaation of appointment based on mobile/aadhar/patient id
   * @function searchCancelPatient
   */
  searchPatient() {
    this.isSelectePatient = true;
    const payload = {
      doctorId: this.empId,
      orgId: this.userSession.orgId,
      searchType: this.searchType,
      patientNm: this.patientForm.get('patientData').value
    };
    this.searchCancelPatientSubscription = this.appointmentService.searchCancelPatient(payload)
      .subscribe(
        response => {
          if (response && response.responseCode === 'E200') {
            this.cancelPatientData = response.data;
            this.isShowptDtl = false;
          }
        }
      );
  }

  /**
   * @description This method is used to get the search type of patient.
   */
  tyepOfPatient() {
    this.selectPatientSearch = new FormControl('mobileNum');
    this.placeholderValue = 'Enter Mobile No';
    this.searchType = 'mobileNum';
    this.selectPatientSearch.valueChanges.subscribe(value => {
      this.searchType = value;
      this.patientData = null;
      switch (this.searchType) {
        case 'name':
          this.placeholderValue = 'Enter Patient Name';
          break;
        case 'aadharNum':
          this.placeholderValue = 'Enter Patient Aadhar No';
          break;
        case 'patientId':
          this.placeholderValue = 'Enter Patient Patient Id';
          break;
        default:
          this.placeholderValue = 'Enter Mobile No';
          break;
      }
    });
  }

  ngOnDestroy() { }
}
