import { Component, OnInit, ViewEncapsulation, OnDestroy } from '@angular/core';
import { UserService, AppointmentApiService, UtilService } from '@core/services';
import { UserSession } from '@core/models/user-session';
import { FormControl, FormGroup, FormBuilder, Validators, AbstractControl } from '@angular/forms';
import { Subject, Observable, concat, of } from 'rxjs';
import { debounceTime, distinctUntilChanged, switchMap, map, filter } from 'rxjs/operators';
import { AutoUnsubscribe } from 'ngx-auto-unsubscribe';
import { DoctorService } from '@app/modules/appointment/services/doctor.service';
@AutoUnsubscribe()
@Component({
  selector: 'app-patientsearch',
  templateUrl: './patientsearch.component.html',
  styleUrls: ['./patientsearch.component.scss'],
  encapsulation: ViewEncapsulation.None
})
export class PatientsearchComponent implements OnInit, OnDestroy {

  checked = this.utilService.generateUniqueId();
  patient$: Observable<any>;
  patientInput$ = new Subject<string>();
  patientSearchForm: FormGroup;
  placeholderValue: string;
  patientControl = new FormControl();
  patientData: any;
  patinetSearchControl: AbstractControl;
  searchType: any;
  doctorData: any;
  orgInfo: any;
  empId: any;
  name = this.utilService.generateUniqueId();

  constructor(
    private userService: UserService,
    private appointmentService: AppointmentApiService,
    private formbuilder: FormBuilder,
    private utilService: UtilService,
    private doctorService: DoctorService,
  ) {
    this.userSession = this.userService.getUser();
  }

  private userSession: UserSession;

  ngOnInit() {
    this.patinetSearchControl = new FormControl('mobileNo');
    this.placeholderValue = 'Enter Your Mobile No';
    this.searchType = 'mobileNum';
    this.patinetSearchControl.valueChanges.subscribe(value => {
      if (value === 'patientName') {
        this.searchType = 'name';
      } else if (value === 'aadharNo') {
        this.searchType = 'aadharNum';
      } else if (value === 'id') {
        this.searchType = 'patientId';
      } else if (value === 'mobileNo') {
        this.searchType = 'mobileNum';
      }
      this.patientData = '';
      switch (this.searchType) {
        case 'P':
          this.placeholderValue = 'Search Patient by Name';
          break;
        case 'A':
          this.placeholderValue = 'Search Patient by Aadhaar Number';
          break;
        case 'R':
          this.placeholderValue = 'Search Patient by Hospital ID';
          break;
        default:
          this.placeholderValue = 'Search Patient by Mobile Number';
          break;
      }
    });
    this.doctorService.doctorDataSubject.subscribe(data => {
      this.doctorData = data;

      if (
        this.doctorData &&
        this.doctorData.organisationInfoList &&
        this.doctorData.organisationInfoList.length
      ) {
        this.orgInfo = this.doctorData.organisationInfoList;
        this.empId = this.orgInfo[0].docId;
      }
    });
  }

  /**
   * @description This method is used to declare list of form control names with validation in a forPatientForm.
   */
  initForm() {
    this.patientSearchForm = this.formbuilder.group({
      patinetSearchControl: this.formbuilder.control(null, Validators.required),
      serviceTable: ['', Validators.required]
    });
  }

  /**
   * @description this api is used to search patients for booked or cancelled appointment list based on mobile/aadhar/patient id
   * @function searchCancelPatient
   */
  searchPatient() {
    this.patient$ = concat(
      of([]),
      this.patientInput$.pipe(
        debounceTime(200),
        distinctUntilChanged(),
        filter(searchText => searchText.length > 3),
        switchMap(search => {
          const payload = {
            doctorId: this.empId,
            orgId: this.userSession.orgId,
            patientNm: search,
            searchType: this.searchType
          };
          return this.appointmentService.searchCancelPatient(payload).pipe(
            map(response => {
              if (response.responseCode === 'E200') {
                return response.data;
              } else if (response.responseCode === 'E400') {
                throw new Error('failure');
              }
            })
          );
        })
      )
    );
  }

  ngOnDestroy() { }
}
