import {
  Component,
  OnInit,
  Input,
  Output,
  EventEmitter,
  Inject,
  AfterViewInit,
  OnDestroy,
  SimpleChanges, OnChanges
} from '@angular/core';
import { DoctorService } from '@app/modules/appointment/services/doctor.service';
import { FormGroup, FormBuilder } from '@angular/forms';
import { MOMENT } from '@core/services';
import { Subscription } from 'rxjs';
import { AutoUnsubscribe } from 'ngx-auto-unsubscribe';
@AutoUnsubscribe()
@Component({
  selector: 'app-calendar-header',
  templateUrl: './calendar-header.component.html',
  styleUrls: ['./calendar-header.component.scss']
})
export class CalendarHeaderComponent implements OnInit, AfterViewInit, OnDestroy, OnChanges {
  @Input()
  view: string;

  @Input()
  viewDate: Date;

  @Input()
  locale = 'en';

  @Output()
  viewChange: EventEmitter<string> = new EventEmitter();

  @Output()
  viewDateChange: EventEmitter<Date> = new EventEmitter();

  private clickedDateSubscription: Subscription;
  private monthDataFromDocScheduleSubscription: Subscription;

  constructor(
    private doctorService: DoctorService,
    @Inject(MOMENT) private moment
  ) { }



  ngOnInit() {
    this.doctorService.viewData.next(this.view);
  }

  ngOnChanges(changes: SimpleChanges) {
    if (changes.view && changes.view.currentValue) {
      this.doctorService.viewData.next(changes.view.currentValue);
      this.viewDate = new Date();
      this.doctorService.calendarData.next(this.viewDate);
      this.doctorService.weekData.next(this.viewDate);
      this.doctorService.tabIndexData.next(0);
    }

    if (changes.viewDate && changes.viewDate.currentValue) {
      if (this.view === 'day') {
        this.doctorService.calendarData.next(changes.viewDate.currentValue);
      }
      if (this.view === 'week') {
        this.doctorService.weekData.next(changes.viewDate.currentValue);
      }
      if (this.view === 'month') {
        this.doctorService.monthData.next(changes.viewDate.currentValue);
      }
    }
  }

  ngAfterViewInit() {
    this.clickedDateSubscription = this.doctorService.clickedDate.subscribe(val => {
      if (val) {
        this.viewDate = new Date(val);
        this.doctorService.weekData.next(this.viewDate);
      }
    });

    this.monthDataFromDocScheduleSubscription = this.doctorService.monthDataFromDocSchedule.subscribe(val => {
      if (val) {
        this.viewDate = new Date(val);
        this.viewDateChange.next(this.viewDate);
      }
    });
  }

  ngOnDestroy() { }
}
