import { Component, OnInit, ViewChild, ViewContainerRef, TemplateRef, Input, OnDestroy, ChangeDetectorRef } from '@angular/core';
import { MatDatepicker, MatSidenav } from '@angular/material';
import { UserService } from '@core/services';
import { UserSession } from '@core/models/user-session';
import { DoctorService } from '../../services/doctor.service';
